.tb_con_theme {
    /* background-color: #f5f5f5; */
    width: 100%;
    padding:0 15px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    position: relative;
}

.tb_con_theme_wrapper{
    position: relative;
    height: 100%;
   
}

.tb_con_container{
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 20px;
}
.tb_con_wrap {
    padding: 5px 0;
    display: flex;
    flex-direction: column;
    width: 100%;
}
.tb_author_profile {
    height: 60px;
    width: 60px;
     background-position: center;
    background-size: cover!important;
    border-radius: 8px;
    border: 1px solid #707070;
    margin-right: 15px;
    background-color: #707070;
    font-size: 30px;
    text-align: center;
    font-weight: bold;
    color: #2b2b2b;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}


.tb_author_profile img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.tb_con_author_name {
    font-weight: 700;
    font-size: 20px;
    text-transform: capitalize;
}
.tb_con_post_time {
    color: #9f9f9f;
    font-size: 14px;
    opacity: 0.5;
}

/* For addtional Requirement */
.tb_con_post_social{
    position: absolute;
   right: 15px;
    bottom: 15px;
}

.tb_con_post_social .tb_icon, .tb_con_post_social .tb_icon2 {
    font-size: 24px;
}
.tb_svg_icon img {
    min-width: 26px;
    width: 26px;
    height: 26px;
    display: inline-block;
}

.tb_con_post_social {
    padding-left: 20px;
}
.tb_con_post_content {
    display: flex;
    overflow-x: hidden;
}
.tb_con_post_content00 {
    overflow-x: hidden;
    background-color: #fff;
    padding: 20px 60px 20px 20px;
    display: inline-block;
    margin-left: 75px;
    max-width: 75%;
    border-top-left-radius: 0px;
    border-top-right-radius: 12px;
    border-bottom-right-radius: 12px;
    border-bottom-left-radius: 12px;
    margin-bottom: 10px;
    -webkit-box-shadow: 0 1px 2px rgb(0 0 0 / 3%), 0 2px 4px rgb(0 0 0 / 2%), 0 5px 14px rgb(0 0 0 / 1%);
    box-shadow: 0 1px 2px rgb(0 0 0 / 3%), 0 2px 4px rgb(0 0 0 / 2%), 0 5px 14px rgb(0 0 0 / 1%);
}

.tb_con_post_cont01 {
    display: flex;
    align-items: self-start;
    justify-content: space-between;
}

.tb_con_post_media {
    max-width: 350px;
    border-radius: 10px;
    overflow: hidden;
}

.tb_con_post_cont002 {
    font-size: 18px;
    color: #232323;
    margin-bottom: 15px;
    line-height: 160%;
    word-break: break-word;
}

.tb_con_post_media img, .tb_con_post_media video, .tb_con_post_media iframe {
    max-width: 100%;
    display: block;
    height: auto;
}

.tb_con_post_media video, .tb_con_post_media iframe {
    background-color: #000000;
    width: 100%;
    min-height: 300px;
    height: 100%;
}

.tb_con_author {
    display: flex;
    align-items: center;
    justify-content: start;
    margin-bottom: 12px;
}

.tb_chat_box_right .tb_author_profile{
    margin-right: 0px;
    margin-left: 15px;
}
.tb_chat_box_right .tb_con_author {
    justify-content: flex-end;
}
.tb_chat_box_right .tb_con_post_content00{
    margin-right: 75px;
    margin-left: auto;
}
.tb_con_title{
    font-weight: 700;
}
.tb_chat_box_right .tb_con_post_content00 {
    border-top-left-radius: 12px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 12px;
    border-bottom-left-radius: 12px;
}

.animate-typing-col {
    transition: all 300ms;
}

.animate-typing-col .tb_dot {
    display: inline-block;
    width: 6px;
    height: 6px;
    margin: 0 3px;
    border-radius: 50%;
    background: #333333;
    animation: wave 1.3s linear infinite;
}

.animate-typing-col .tb_dot:nth-child(2) {
    -webkit-animation-delay: -1.1s;
    animation-delay: -1.1s;
}

.animate-typing-col .tb_dot:nth-child(3) {
    -webkit-animation-delay: -.9s;
    animation-delay: -.9s;
}


@-webkit-keyframes wave {
	0%,
	100%,
	60% {
		-webkit-transform: initial;
		transform: initial
	}
	30% {
		-webkit-transform: translateY(-5px);
		transform: translateY(-5px)
	}
}

@keyframes wave {
	0%,
	100%,
	60% {
		-webkit-transform: initial;
		transform: initial
	}
	30% {
		-webkit-transform: translateY(-5px);
		transform: translateY(-5px)
	}
}



.tb_con_post_content .tb_con_post_cont01{
    display: none;
}

.tb_con_wrap.tb_left_anim .animate-typing-col,
.tb_con_wrap.tb_right_anim .animate-typing-col{
    display: none;
}

.tb_con_wrap.tb_left_anim .tb_con_post_cont01,
.tb_con_wrap.tb_right_anim .tb_con_post_cont01{
    display: flex;
  
    
}

.tb_con_wrap.tb_left_anim .tb_con_post_content00{
	-webkit-animation: scale-in-tl 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
            animation: scale-in-tl 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both; 
                position: relative;
                min-height: 58px;
}
@-webkit-keyframes scale-in-tl {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
      -webkit-transform-origin: 0% 0%;
              transform-origin: 0% 0%;
      opacity: 0;
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      -webkit-transform-origin: 0% 0%;
              transform-origin: 0% 0%;
      opacity: 1;
    }
  }
  @keyframes scale-in-tl {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
      -webkit-transform-origin: 0% 0%;
              transform-origin: 0% 0%;
      opacity: 0;
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      -webkit-transform-origin: 0% 0%;
              transform-origin: 0% 0%;
      opacity: 1;
    }
  }
  

.tb_con_wrap.tb_chat_box_right.tb_right_anim .tb_con_post_content00{
	-webkit-animation: scale-in-tr 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
            animation: scale-in-tr 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both; 
            position: relative;
            min-height: 58px;
}

@-webkit-keyframes scale-in-tr {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
      -webkit-transform-origin: 100% 0%;
              transform-origin: 100% 0%;
      opacity: 0;
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      -webkit-transform-origin: 100% 0%;
              transform-origin: 100% 0%;
      opacity: 1;
    }
  }
  @keyframes scale-in-tr {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
      -webkit-transform-origin: 100% 0%;
              transform-origin: 100% 0%;
      opacity: 0;
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      -webkit-transform-origin: 100% 0%;
              transform-origin: 100% 0%;
      opacity: 1;
    }
  }
  



.tb_con_wrap.tb_right_anim .tb_author_profile,
.tb_con_wrap.tb_left_anim .tb_author_profile{
    -webkit-animation-name: bounceIn;
    animation-name: bounceIn;
    -webkit-animation-duration: .75s;
    animation-duration: .75s;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}
@-webkit-keyframes bounceIn {
    0%, 20%, 40%, 60%, 80%, 100% {
        -webkit-transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
        transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
    }
    0% {
    opacity: 0;
        -webkit-transform: scale3d(.3, .3, .3);
        transform: scale3d(.3, .3, .3);
    }
    20% {
        -webkit-transform: scale3d(1.1, 1.1, 1.1);
        transform: scale3d(1.1, 1.1, 1.1);
    }
    40% {
        -webkit-transform: scale3d(.9, .9, .9);
        transform: scale3d(.9, .9, .9);
    }
    60% {
        opacity: 1;
        -webkit-transform: scale3d(1.03, 1.03, 1.03);
        transform: scale3d(1.03, 1.03, 1.03);
    }
    80% {
        -webkit-transform: scale3d(.97, .97, .97);
        transform: scale3d(.97, .97, .97);
    }
    100% {
        opacity: 1;
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
    }
}
@keyframes bounceIn {
    0%, 20%, 40%, 60%, 80%, 100% {
        -webkit-transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
        transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
    }
    0% {
    opacity: 0;
        -webkit-transform: scale3d(.3, .3, .3);
        transform: scale3d(.3, .3, .3);
    }
    20% {
        -webkit-transform: scale3d(1.1, 1.1, 1.1);
        transform: scale3d(1.1, 1.1, 1.1);
    }
    40% {
        -webkit-transform: scale3d(.9, .9, .9);
        transform: scale3d(.9, .9, .9);
    }
    60% {
    opacity: 1;
        -webkit-transform: scale3d(1.03, 1.03, 1.03);
        transform: scale3d(1.03, 1.03, 1.03);
    }
    80% {
        -webkit-transform: scale3d(.97, .97, .97);
        transform: scale3d(.97, .97, .97);
    }
    100% {
        opacity: 1;
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
    }
} 


@media screen and (max-width: 767px) {
.tb_con_container {
    padding-left: 10px;
    padding-right: 10px;
}   
.tb_con_theme{
    padding: 0 10px;
}
.tb_author_profile {
    height: 45px;
    width: 45px;
}
.tb_con_post_social {
    padding-left: 15px;
}
.tb_con_post_content00 {
    padding: 15px 40px 15px 15px;
    display: inline-block;
    margin-left: 60px;
    max-width: calc(100% - 60px);
}
.tb_con_author_name {
    font-size: 18px;
    text-transform: capitalize;
}
.tb_con_post_time {
    font-size: 12px;
}
.tb_chat_box_right .tb_con_post_content00 {
    margin-right: 60px;
}
.tb_con_post_cont002 {
    font-size: 14px;
}
.tb_con_post_media {
    max-width: 250px;
    border-radius: 5px;
}
}


