.tb_scroll_to_bottom button {
    border-radius: 0px;
    width: 50px;
    height: 50px;
    color: #fff;
    background-color: rgba(255,64,80, .8);
    font-family: FontAwesome;
    box-shadow: 0 0 0 0 rgb(255,64,80);
    animation: pulse-black 2s infinite;
    display: inline-block;
}
.tb_scroll_to_bottom button:hover{
background-color: rgba(255,64,80, .8);
}
.tb_scroll_to_bottom button:before {
   content: "\f063";
}

@keyframes pulse-black {
	0% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(255,64,80, .7)
	}
	70% {
		transform: scale(1)
	}
	99% {
		box-shadow: 0 0 0 10px rgba(255,64,80, 0)
	}
	100% {
		transform: scale(.85);
		box-shadow: 0 0 0 0 rgba(255,64,80, 0)
	}
}