.tb_wall_wrap__ {
    display: inline-block;
    width: 100%;
    height: 100vh;
}
.themeStart40 {
    overflow: hidden;
    height: 100vh;
}

.tb_wall_list_wrap___{
    float: left;
    width: 20%;
    transition: transform 1s linear;
}

.tb_wall_card_in__ {
    width: 100%;
    background-color: #fff;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.08);
    border-radius: 7px;
    overflow: hidden;
}

.card_img__01 {
    display: table;
    width: 100%;
    position: relative;
}

.tb_fc_play_icon {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
}

.tb_fc_play_icon001 {
    width: 15%;
    max-width: 50px;
    height: auto;
}

.card_img_02 {
    width: 100%;
    display: block;
    margin: 0 auto;
    height: auto;
}
.card_video__01{
    display: table;
    width: 100%;
}
.card_video__01 iframe {
    width: 100%;
    height: 100%;
}
.tb_post_vid_005 {
    width: 100%;
    border: none;
    height: 100%;
    display: block;
    background-color: #000;
}

.tb_wall_card_wrap__ {
    display: block;
    padding: 10px;
    width: 100%;
}

.tb_wall_card_content___ {
    padding: 15px;
}

.tb_wall_card_conent__001 {
    position: relative;
}

.tb_post_author_001_ {
    height: 55px;
    width: 55px;
    border-radius: 50%;
    margin: 0 auto;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    margin-bottom: 10px;
}

.tb_post_userinfo__ {
    padding-bottom: 10px;
}

.tb_post_username__ {
    text-align: center;
    font-size: 14px;
    line-height: 18px;
    font-weight: 600;
    margin: 0px auto 4px auto;
    max-width: 80%;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.tb_post_author_001_ img{
    display: block;
    border-radius: 50%;
    /* text-align: center; */
    /* margin: auto; */
    width: 100%;
}
.tb_post_002 {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}

.tb_post_user_003 {
    font-size: 11px;
    line-height: 13px;
    color: #2b2b2b;
    font-weight: 500;
    max-width: 50%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.tb_seprate_dot__ {
    width: 4px;
    height: 4px;
    background-color: #2b2b2b;
    border-radius: 50%;
    margin: 0 5px;
}

.tb_post_time_001 {
    font-size: 11px;
    line-height: 13px;
    color: #2b2b2b;
    font-weight: 500;
    max-width: 40%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.tb_card_social_001 {
    text-align: center;
    width: 100%;
    margin-bottom: 10px;
}

.tb_card_social_001 .fa {
    font-size: 26px;
    /* color: #2b2b2b; */
}

.tb_post_content__05 {
    margin-bottom: 10px;
    overflow: hidden;
}

.tb_post_cont_in__001 {
    text-align: center;
    font-size: 14px;
    line-height: 150%;
    display: block;
    word-break: break-all;
}



@font-face {
    font-family: 'FontAwesome';
    src: url('https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/fonts/fontawesome-webfont.eot?v=4.7.0');
    src: url('https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/fonts/fontawesome-webfont.eot?#iefix&v=4.7.0') format('embedded-opentype'), url('https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/fonts/fontawesome-webfont.woff2?v=4.7.0') format('woff2'), url('https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/fonts/fontawesome-webfont.woff?v=4.7.0') format('woff'), url('https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/fonts/fontawesome-webfont.ttf?v=4.7.0') format('truetype'), url('https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/fonts/fontawesome-webfont.svg?v=4.7.0#fontawesomeregular') format('svg');
    font-weight: normal;
    font-style: normal;
}

.tb_post_cont_in__001.decorateTxt p::after,
.tb_post_cont_in__001.decorateTxt p::before {
    
    font-family: FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.tb_post_cont_in__001.decorateTxt p::before {
    content: "\f10d";
    margin: 0 10px 0 0;
    text-align: left;
    position: relative;
    top: -5px;
}
.tb_post_cont_in__001.decorateTxt p::after {
    content: "\f10e";
    margin: 5px 0 0 10px;
    text-align: right;
    position: relative;
    top: 5px;
}




.tb_wall_wrap__ .swiper-container {
    width: 100%;
    height: 100%;
}
  
.tb_wall_wrap__ .swiper-slide {
    height: auto;
}


.tb_wall_wrap__ .tb_fc_animation:nth-child(even) {
	-webkit-animation: tb_bounce-in-top 1.5s both;
	        animation: tb_bounce-in-top 1.5s both;
            opacity: 1;
}

.tb_wall_wrap__ .tb_fc_animation:nth-child(odd) {
	-webkit-animation: tb_bounce-in-bottom 1.5s both;
	        animation: tb_bounce-in-bottom 1.5s both;
            opacity: 1;
}

.tb_wall_list_wrap___{
    opacity: 0;
}

.taggbox-display-root .tb_post_cont_rating{
    margin-bottom: 15px;
    display: block;
    text-align: center;
}
.taggbox-display-root .tb_post_cont_in__001.decorateTxt div:after, .taggbox-display-root .tb_post_cont_in__001.decorateTxt div:before {
    font-family: FontAwesome;
