ul {
  list-style: none;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  *zoom: 1;
}

.wall_view_wrapper .tb_countdown_timer {
  max-width: 70%;
}

.tb_countdown_body_class {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #888;
  text-shadow: 0 1px 0 rgb(0 0 0 / 30%);
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  flex-direction: column;
}

.tb_countdown_body_class.tb_light {
  background: #fff;
  background: radial-gradient( ellipse at center, rgba(255, 255, 255, 1) 0%, rgb(255, 255, 255) 100% );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr = '#255', endColorstr = '#255', GradientType = 1);
}

.tb_countdown_body_class.tb_dark{
  background: rgb(150, 150, 150);
  background: radial-gradient( ellipse at center, rgba(150, 150, 150, 1) 0%, rgb(114, 114, 114) 100% );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr = '#969696', endColorstr = '#595959', GradientType = 1);
}

.tb_countdown_body_class.tb_light .tb_time_unit>div {
  color: #333;
}

.tb_countdown_body_class.tb_dark .tb_time_unit>div {
  color: #fff;
}

.tb_countdown_body_class.tb_light .tb_countdown_label, 
.tb_countdown_body_class.tb_light .tb_after_complete{
  color: #333;
}

.tb_countdown_label,
.tb_after_complete {
  color: #fff;
  font-size: 22px;
  margin-bottom: 20px;
  text-align: center;
  display: block;
  word-break: break-all;
}


.tb_countdown_timer {
  width: 100%;
  text-align: center;
  display: flex;
  align-items: start;
  justify-content: center;
}

.tb_countdown_body_class .container {
  text-align: center;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 140px;
  height: 90px;
  margin: -45px 0 0 -70px;
}


.tb_count_002 {
  position: relative;
  margin:0 .7vw;
  width: 100%;
  display: flex;
  flex-flow: column;
}

.tb_time_unit {
  color: #fff;
  display: block;
  text-align: center;
  text-transform: uppercase;
  font-weight: 400;
  margin-top: 5px;
}

.tb_time_unit>div {
  font-size: 1vw;
  fill: #fff;
  letter-spacing: 1px;
  line-height: 2;
}

.tb_flip_00 {
  position: relative;
  margin: .2vw;
  width: 100%;
  padding-bottom: 60%;
  padding-top: 10px;
  z-index: 1;
}

.tb_flip_00:nth-child(2) {
  z-index: 2;
}

.tb_flip_00:nth-child(3) {
  z-index: 3;
}

.tb_flip_00:last-child {
  z-index: 4;
}

.tb_flip_00 .tb_flip_001 {
  z-index: 1;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
  border-radius: 3px;

}
.tb_flip_00 .tb_flip_001:first-child {
  z-index: 2;
}
.tb_count_003 {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: center;
}

.tb_flip_00 .tb_flip_001 a {
  display: block;
  height: 100%;
  perspective: 300px;
}

.tb_flip_00 .tb_flip_001 a div {
  z-index: 1;
  position: absolute;
  left: 0;
  width: 100%;
  height: 50%;
  overflow: hidden;
}

.tb_flip_00 .tb_flip_001 a div .shadow {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 2;
}

.tb_flip_00 .tb_flip_001 a div.up {
  transform-origin: 50% 100%;
  top: 0;
}

.tb_flip_00 .tb_flip_001 a:after {
  content: "";
  position: absolute;
  top: 50%;
  bottom: 0;
  left: 0;
  z-index: 5;
  margin-top: -1px;
  height: 1px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}

.tb_flip_00 .tb_flip_001 a div.down {
  transform-origin: 50% 0%;
  bottom: 0;
}

.tb_flip_00 .tb_flip_001 a div .inn {
  position: absolute;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 200%;
  color: #ccc;
  text-shadow: 0 1px 2px #000;
  text-align: center;
  background-color: #333;
  border-radius: 3px;
}
.tb_flip_00 .tb_flip_001 a .inn text {
  font-weight: bold;
  fill:#fff;
  font-size: 2em;
}

.wall_view .tb_flip_00 .tb_flip_001 a .inn text {
  font-size: 1.26em;
}

.tb_flip_00 .tb_flip_001 a div.up .inn {
  top: 0;
}

.tb_flip_00 .tb_flip_001 a div.down .inn {
  bottom: 0;
}

/* PLAY */

.tb_countdown_body_class .tb_countdown_timer .tb_flip_00 .tb_flip_001.before {
  z-index: 3;
}

.tb_countdown_body_class .tb_countdown_timer .tb_flip_00 .tb_flip_001.active {
  animation: asd 0.5s 0.5s linear both;
  z-index: 2;
}

@keyframes asd {
  0% {
    z-index: 2;
  }
  5% {
    z-index: 4;
  }
  100% {
    z-index: 4;
  }
}

.tb_countdown_body_class .tb_countdown_timer .tb_flip_00 .tb_flip_001.active .down {
  z-index: 2;
  animation: turn 0.5s 0.5s linear both;
}

@keyframes turn {
  0% {
    transform: rotateX(90deg);
  }
  100% {
    transform: rotateX(0deg);
  }
}

.tb_countdown_body_class .tb_countdown_timer .tb_flip_00 .tb_flip_001.before .up {
  z-index: 2;
  animation: turn2 0.5s linear both;
}

@keyframes turn2 {
  0% {
    transform: rotateX(0deg);
  }
  100% {
    transform: rotateX(-90deg);
  }
}

/* SHADOW */

.tb_countdown_body_class .tb_countdown_timer .tb_flip_00 .tb_flip_001.before .up .shadow {
  background: -moz-linear-gradient(
    top,
    rgba(0, 0, 0, 0.1) 0%,
    rgba(0, 0, 0, 1) 100%
  );
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(0%, rgba(0, 0, 0, 0.1)),
    color-stop(100%, rgba(0, 0, 0, 1))
  );
  background: linear-gradient(
    top,
    rgba(0, 0, 0, 0.1) 0%,
    rgba(0, 0, 0, 1) 100%
  );
  background: -o-linear-gradient(
    top,
    rgba(0, 0, 0, 0.1) 0%,
    rgba(0, 0, 0, 1) 100%
  );
  background: -ms-linear-gradient(
    top,
    rgba(0, 0, 0, 0.1) 0%,
    rgba(0, 0, 0, 1) 100%
  );
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.1) 0%,
    rgba(0, 0, 0, 1) 100%
  );
  animation: show 0.5s linear both;
}

.tb_countdown_body_class .tb_countdown_timer .tb_flip_00 .tb_flip_001.active .up .shadow {
  background: -moz-linear-gradient(
    top,
    rgba(0, 0, 0, 0.1) 0%,
    rgba(0, 0, 0, 1) 100%
  );
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(0%, rgba(0, 0, 0, 0.1)),
    color-stop(100%, rgba(0, 0, 0, 1))
  );
  background: linear-gradient(
    top,
    rgba(0, 0, 0, 0.1) 0%,
    rgba(0, 0, 0, 1) 100%
  );
  background: -o-linear-gradient(
    top,
    rgba(0, 0, 0, 0.1) 0%,
    rgba(0, 0, 0, 1) 100%
  );
  background: -ms-linear-gradient(
    top,
    rgba(0, 0, 0, 0.1) 0%,
    rgba(0, 0, 0, 1) 100%
  );
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.1) 0%,
    rgba(0, 0, 0, 1) 100%
  );
  animation: hide 0.5s 0.3s linear both;
}

/*DOWN*/

