/*--Content Play Start--*/
body {
	margin: 0px;
}
iframe{border:0; outline:0;}
/* Content Play Loader */
/* .loaderBox{ width:262px; position:absolute; left:50%; top:50%; 
      margin-left:-231px;
} */
.loaderBox {
          position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
    }
.logo{}
.progressDiv{ margin-top:10px;}
#overlayLoader{
    position:fixed;
    z-index:99999;
    top:0;
    left:0;
    bottom:0;
    right:0;
    background:rgba(0,0,0,1);
    transition: 1s 0.4s;
    -webkit-transition: 1s 0.4s;
}
.progressBlock
{
    background: #4d4d4d;
    height: 6px;
    width: 100px;
    margin: 0 auto;
    border-radius: 4px;
    -moz-border-radius: 4px;
    -webkit-border-radius: 4px;
    position: fixed;
    left: 0;
    right: 0;
}
.bar 
{ 
    background-color: #ffffff; 
    width:0%; 
    height:5px; 
    border-radius: 4px; 
    -moz-border-radius: 4px; 
    -webkit-border-radius: 4px; 
}
.percent 
{ 
    position:absolute; 
    display:inline-block; 
    top:3px; 
    left:48%; 
}
/* End Content Play Loader */

.mixheader { padding: 10px 35px; overflow: hidden}
.mixheader a.commanBtn{font-weight: 300; float:right; padding:6px 10px; background:none; border:1px solid #393838; box-shadow:inset 0px 1px 0px #000; -webkit-box-shadow:inset 0px 1px 0px #000; -moz-box-shadow:inset 0px 1px 0px #000;}
.mixheader a.commanBtn i{ margin:2px 6px 0 0; font-size: 14px;}
.mixheader a.commanBtn:hover{ background:#393838; box-shadow:none; -webkit-box-shadow:none; -moz-box-shadow:none;}
.mixTop{margin-top: 20px;}
.mixTop .displaymix{ 
    float: right;    
    color: #fff;
    text-transform: uppercase; 
    padding: 12px 30px;
    font:400 16px/16px "Lato";
}
.mixInnerdiv{
    margin: 15px 0; display:table; width:100%;
}
.mixInnerdiv .row{margin:0px; background:#fff; display:table-row;}
.mixInnerdiv .col-md-3{ 
    width:23%; 
    padding:0px; 
    display:table-cell; 
    float: none;
    border-left: 1px solid #e1e1e1;      
    border-top: 1px solid #e1e1e1; 
    position:relative;
	vertical-align:top;
}
.mixInnerdiv .col-md-9{padding:0px; width:77%; display:table-cell; float: none; border: 1px solid #d85262; vertical-align:top;}

.mixsidebar{
    background: #fcfcfc;
    margin-bottom: 55px;
}
.mixAddscene {
    display: block;
    padding: 7px 20px;
    font: 400 16px/30px "Open Sans";
    color: #fff;
    transition: all 300ms ease-in-out 0s;
    -webkit-transition: all 300ms ease-in-out 0s;
    background: #d85262;
    opacity: 1;
    position: absolute;
    bottom: 0px;
    width: 100%;
    text-transform:uppercase;
    z-index: 1;
	height: 43px;
}
.mixAddscene i{ font-size:18px; margin-right:5px;}
.mixAddscene img{
    float: right;
}
.mixAddscene:hover, .mixAddscene:visited, .mixAddscene:focus{
    color: #fff;
    text-decoration: none;
    opacity: 0.9;
}
.mixsidebarlist{
    padding: 0;
    margin: 0;
    list-style-type: none;
}
.mixsidebarlist li{
    padding: 10px;
    display: block;
    background: #ebebeb;
    margin-bottom:1px;
}
.mixsidebarlist li.active{
    background: #d85262;
}
.mixsidebarlist li.active *{ color:#fff !important;}
.mixsidebarlist li.active .sceneTime{ border-color:#cc525c; background:#d3525d;}
.mixsidebarlist .sceneTop h3{
    color: #333333;
    font:400 16px/16px "Lato";
    float: left;
    margin: 0;
}
.mixsidebarlist .sceneTop h3 i{
    cursor: move;
}
.mixsidebarlist .sceneTop .sceneTime{
    font: 300 12px "Lato";
    color: #666;
    float: right;
    border: 1px solid #cbcbcb;
    padding: 2px 4px;
    border-radius: 2px;
}
.mixsidebarlist .screenhorizontal{
    width: 100%;
    margin: 10px auto;
}
.screenhorizontal .horTop{
    background: #f7f7f7;
    width: 100%;
    height: 53px;
    display: block;
}
.screenhorizontal .horBottom{
    background: #c8c8c8;
    width: 100%;
    height: 53px;
    display: block;
}
.mixsidebarlist .screenVertical{
    width: 100%;
    margin: 10px auto;
}
.screenVertical .vertLeft{
    background: #f7f7f7;
    width: 50%;
    height: 115px;
    float: left;
}
.screenVertical .vertRight{
    background: #c8c8c8;
    width: 50%;
    height: 115px;
    float: right;
}
.fullscreenscene{
    width: 100%;
    margin: 10px auto;
    height: 115px;
    background: #f7f7f7;	
}

.screenlabel {position:relative; padding-top:8px;}
.screenlabel:after{
    position: absolute;
    content: '';
    top: 0px;
    left: -10px;
    right: -10px;
    height: 1px;
    background: #dadada;
}
.mixsidebarlist li.active .screenlabel:after{ background:#d44f5b;}
.screenlabel .screenType{
    color: #333333;
    font: 400 14px/14px "Lato";
    float: left;
}
.screenlabel a.pull-right{ margin-right:3px;}

.screenlabel i{
    color: #b6b6b6;
    font-size: 18px;
    margin-right:3px;
}

.mixRightPart{
    padding: 10px 0px;
    height: inherit;
}
.tab-content.mixRightPart .row{display:block;}
.screenIndicator
{
    float: left;
    width: 20px;
    height: 15px;
    border: 1px solid #acacac;
    margin-right: 8px;
    position: relative;
}
.sectionType
{
    color: #333333;
    font:400 17px/15px "Lato";
}
.sectionDuration{
    color: #666666;
    font: 400 12px "Lato";
    display: inline-block;
}
.sectionDuration span{
    color: #333333;
    font-weight: 400;
}
.sceneItemList{
    list-style-type: none;
    margin: 0;
    padding: 0;
}
.sceneItem{
    background: #fff;    
    display: block;
    margin-bottom: 10px;
    border:1px solid #f6f6f6;	
    transition:all ease 0.3s;
    -moz-transition:all ease 0.3s;
    -webkit-transition:all ease 0.3s;
    padding: 12px 0px;
}
.sceneItem:hover{
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2);
    -webkit-box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2);
}
.sceneItem .row{
    margin-top: 0;
    margin-bottom: 0;
    display:block;
}
.drag-handle{
    cursor: move;
    position: absolute;
    width: 47px;
    height: 47px;
    border-right:1px solid #f1f1f1;
    left: 0px;
    top: -12px;
    text-align: center;
    line-height: 45px;
}
.sceneItemTitle{
    color: #5a5a5a;
    font:300 14px "Lato";
    display: inline-block;
    margin-left: 50px;
	word-break: break-all;
}
.sceneItem i{ color: #9a9a9a !important; font-size:14px; vertical-align: middle;}
.sceneItemTitle i{margin-right:5px; margin-top:-1px;}
.sceneSetting{
    text-align: right;
}

.sceneSetting a {
    margin: 0px 3px;
}
.sceneTimeDrop{
    display: inline-block;  
    margin-left: 5px;
}
.sceneSetting a.dropdown-toggle {
    color: #5a5a5a;
    font: 400 12px "Lato";
    text-decoration: none;
    border: 1px solid #f1f1f1;
    margin: 0;
    padding: 3px 5px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    display: block;
}
.sceneTimeDrop.dropdown.open>.dropdown-toggle{
    border-color: #e1e1e1;
}
.sceneTimeDrop.dropdown>.dropdown-menu:before, .sceneTimeDrop.dropdown>.dropdown-menu:after{
    content: initial;
}
.sceneTimeDrop .dropdown-menu{
    left:auto;
    right: 0;
    font:400 14px "Lato";
    padding: 10px;
    min-width: 165px;
}
.sceneTimeDrop .dropdown-menu .form-control{
    width: 40px;
    padding: 3px 5px;
    height: 25px;
    border-radius: 2px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    margin: 0px 5px;
    display: inline-block;
    font-size: 12px;
    text-align: center;
}
.sceneTimeDrop .timeSave{
    display: block;
    width: 100%;
    margin: 10px 0px 0;
    background: #fff;
    border: 1px solid #d3d3d3;
    color: #333;
    font: 400 12px "Lato";
}
.additemsFullwidth{
    height: auto;
}
.additemsFullwidth-inner{ 
    font: 400 17px "Lato";
    display: block;
    padding: 9px 15px;
    margin: 20px auto 10px;
    text-align: center;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    color: #626262;
    width: 130px; 
    cursor: pointer;
    color:#fff;
    background:#d85262; 
}
.additemsFullwidth-inner i{margin-right:4px;}
.itemurlList {
    padding: 15px !important;
    width: 400px !important;
    margin: 0 auto !important;
    background: #ffffff;
    border: 1px solid #f7f7f7;
    list-style-type: none;
    display: none;
    max-width: 100%;
    text-align: center;
}
.itemurlList li{
    width: 70px;
    height: 70px;
    padding: 0 0.333em 0 0.333em;
    margin: 4px 0px;
    display: inline-block;
}
.itemurlList li a{
    display: block;
    border-radius: 0;
    -moz-border-radius: 0;
    -webkit-border-radius: 0;
    background: #ebebeb;
    line-height: 62px;
    text-align: center;
    transition: all 300ms ease-in-out 0s;
    -webkit-transition: all 300ms ease-in-out 0s;
}
.itemurlList li a:hover{
    background: #e0e0e0;
}
.itemurlList li a i{
    font-size: 24px;
    vertical-align: middle;
}
.itemurlList li a i.fa-film{
    color: #c33f7b;
}
.itemurlList li a i.fa-link{
    color: #292828;
}
.itemurlList li a i.fa-image{
    color: #36c6d3;
}
.itemurlList li a i.fa-youtube{
    color: #BF221F;
}
.itemurlList li a i.fa-vimeo{
    color: #5DA9D5;
}
.itemurlList li .tooltip{
    z-index: 999 !important;
}

.mainTop{
    width:100%;
    height:50%;
    overflow-x: hidden;
    position: relative;
    padding-bottom: 20px;
    border-bottom: 2px solid #e6e6e6;
}
.blankMainTop{
    border-bottom: 2px solid #e6e6e6;
    padding-bottom: 20px;
}
.mainBottom{
    height:50%;
    width: 100%;
    padding-top: 25px;
}
.screeindicatorTop:after{
    content: "";
    border-bottom: 1px solid #ccc;
    width: 100%;
    height: 7px;
    position: absolute;
    top: 0;
}
.screeindicatorBottom:after{
    content: "";
    background: #c8c8c8;
    width: 100%;
    height: 7px;
    position: absolute;
    bottom: 0;
}
.screenindicatorLeft:after{
    content: "";
    background: #fff;
    border-right:1px solid #ccc;
    width: 50%;
    height: 13px;
    position: absolute;
    top: 0;
    left:0
}
.screenindicatorRight:after{
    content: "";
    background: #c8c8c8;
    width: 50%;
    height: 13px;
    position: absolute;
    top: 0;
    right:0;
}
.sectionHeader{ padding:7px 0 12px 0; border-bottom:1px solid #f4f4f4; margin-bottom: 15px;}
.sectionHeaderRight{
    float: right;
}
.sceneHeight, .sceneWidth {
    color: #666666;
    font: 400 12px "Lato";
    display: inline-block;
    margin-right: 10px;
}
.sceneHeight .dropdown-toggle, .sceneWidth .dropdown-toggle{
    color: #333;
    text-decoration: none;
}
.dragbar{
    background-color:black;
    width:100%;
