}
.vertDivide{
    float: left; 
    padding-right: 15px;
}
.rightSecDrag.vertDivide{
    padding-right:0px;
    padding-left:15px;
}
#mainBlankLeft{
    height:100%;
    width: 50%;
}
#mainBlankRight{
    height:100%;
    width: 50%;
    position: relative;
}
#dragbar1{
    background-color: #d0d0d0;
    width: 1px;
    height: 100%;
    cursor: col-resize;
    position: absolute;
    top: 0px;
