#scroll {
    width: 100% !important;
    height: 535px;
    overflow: hidden;
}
.track3 {
    width: 7px;
    background: rgba(0, 0, 0, 0);
    margin-right: 0px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -webkit-transition: background 250ms linear;
    transition: background 250ms linear;
    background: rgba(0, 0, 0, 0.15);
}
.handle3 {
    width: 7px;
    right: 0;
    background: #b7b3b3;
    border-radius: 7px;
    -moz-border-radius: 7px;
    -webkit-border-radius: 7px;
    -webkit-transition: width 250ms;
    transition: width 250ms;
}
#scroll + div {
    opacity: 1 !important;
}
/*Choose Layout*/
.chooseLayoutPopup{
    margin: 25px;
}
.fullscreen{
    border: 5px solid #ccc;
    height: 180px;  
}
.horizontal{
    border: 5px solid #ccc;
    height: 180px;
    position: relative;
}
.horizontal:after{
    position: absolute;
    top:50%;
    background: #ccc;
    content: "";
    width: 100%;
    height: 5px;
}
.vertical{
    border: 5px solid #ccc;
    height: 180px;
    position: relative;
}
.vertical:after{
    position: absolute;
    top:0;
    background: #ccc;
    content: "";
    width: 5px;
    height: 100%;
    left: 50%;
}
.layoutName{ margin-bottom:40px; color:#828282; font-size:17px; text-align:center;}
.fullscreen.selectLayout{
    border: 5px solid #d85262;
}
.horizontal.selectLayout{
    border: 5px solid #d85262;
}
.horizontal.selectLayout:after{
    background: #d85262;
}
.vertical.selectLayout{
    border: 5px solid #d85262;
}
.vertical.selectLayout:after{
    background: #d85262;
}

/*Content Mix panel*/
.contentMix{
    position: absolute;
    width: 100%;
    height: 100%;
    background: black;
}
.contentScreen{
    position: absolute;
    width: 100%;
    height: 100%;
}
.contentScreen .screenLeft{
    position: absolute;
    width: 50%;
    height: 100%;
    left: 0;
    top:0;
}
.contentScreen .screenRight{
    position: absolute;
    width: 50%;
    height: 100%;
    left: 50%;
    top:0;
}
.screenImg {
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    background-color: black;
    width: 100%;
    height: 100%;
}
.screenVideo{
    position: absolute;
    top:50%;
    transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	-webkit-transform: translateY(-50%);
}
.contentScreen .screenTop{
    position: absolute;
    width: 100%;
    height: 50%;
    left: 0;
    top:0;
}
.contentScreen .screenBottom{
    position: absolute;
    width: 100%;
    height: 50%;
    left: 0;
    top:50%;
}
.contentScreen#horScreen .screenVideo{
    top: 0;
    transform: translate(-50%);
	-ms-transform: translate(-50%);
	-webkit-transform: translate(-50%);	
    height: 100%;
    left: 50%;
}
.commanBtn.triggerFullScreen {
    background: #c83b77;
    background: -moz-linear-gradient(45deg,#c83b77 0%,#e06a73 100%);
    background: -webkit-linear-gradient(45deg,#c83b77 0%,#e06a73 100%);
    background: linear-gradient(45deg,#c83b77 0%,#e06a73 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#c83b77',endColorstr='#e06a73',GradientType=1 );
    color: #fff;
    font: 400 14px/18px 'open sans',sans-serif;
    margin-right: 3px;
    display: inline-block;
    vertical-align: middle;
    transition: all ease .3s;
    -moz-transition: all ease .3s;
    -webkit-transition: all ease .3s;
    cursor: pointer;
    border-radius: 7px;
    -webkit-border-radius: 7px;
    -moz-border-radius: 7px;
    text-decoration: none;
    text-transform: uppercase;
    position: fixed; 
    top: 15px; right: 15px; 
    z-index: 9999;
    text-decoration:none;
    padding: 20px 25px; 
    background: #F2545B;
    box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.30);
    font-size: 16px;
    font-weight: 600;
}
.commanBtn.triggerFullScreen:hover{ color:#fff;} 
