/*Responsive*/
@media (max-width: 1024px) {
	.mixheader{ padding:10px 0px;}
    .mixInnerdiv .col-md-9{ width:74%;}
    .mixInnerdiv .col-md-3 {width: 26%;}
}   
@media only screen and (min-width:768px) and (max-width:1024px){
    .mixAddscene{ padding: 11px 10px; }    
    .mixsidebarlist .sceneTop h3{ font-size: 12px;}
    .mixsidebarlist .sceneTop .sceneTime{ font-size: 10px;}
}
@media (max-width: 767px) {
	.mixheader{padding:10px 0;}
    .mixInnerdiv, .mixInnerdiv .row{ display:block;}
    .mixInnerdiv .col-md-3.col-sm-4.pR0, .mixInnerdiv .col-md-9.col-sm-6{ width:100%; float:none; clear:both; display:block;}
    .mixInnerdiv .col-md-3.col-sm-4.pR0 #scroll{ height:auto !important; max-height:500px; }
    .mixsidebar{ margin-bottom:15px;}
    .mixAddscene{position:static; margin-bottom:1px;}
    .drag-handle{border-right:0px;}
    .itemurlList li {width: 50px;height: 50px;}
    .itemurlList li a {line-height:42px;}
}
@media (max-width: 640px) {
	.vertDivide, .rightSecDrag.vertDivide{
        float: none;
        width: 100% !important;
        clear: both;
        padding-right: 0px;
    }
    #dragbar1{ display:none;} 
	.cntntPlayCntrl .modal h4.modal-title{ font-size:22px !Important;}
	.cntntPlayCntrl .modal .commanBtn.btn-sm.backBtn{ top:-70px;}
	.contentPopups iframe, .contentPopups video{max-width:100%;}
}
@media (max-width: 480px) {
	.chooseLayoutPopup .col-xs-4 {
		width: 100%;
		float: none;
		clear: both;
	}
	.cntntPlayCntrl .modal h4.modal-title{ font-size:18px !Important;}
}
