body, h1, h2, h3, h4, h5, h6 { font-family: 'Open Sans', sans-serif }

.taggboxBranding_upgrade_popup.upgredeMain .laptop p { padding:0 30px; margin-top:-38px; font-size:14px }
.taggboxBranding_upgrade_popup .buttonUpgred a { border-radius:4px; font-size:12px }

/* RESPONSIVE */
@media screen and ( max-width : 480px){
    .taggboxBranding_upgrade_popup.upgredeMain .laptop p { margin-top:-18px }
}
.noPostContent {
    position: relative;
    display: inline-block;
    width: 100%;
    min-height: 100vh;
}
.noPostMsgBox {
    position: absolute;
    height: auto;
    width: 90%;
    max-width: 800px;
    left: 50%;
    top: 50%;
    padding: 50px 20px;
    border-radius: 4px;
    background: rgb(255, 255, 255);
    z-index: 5;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    box-shadow: rgb(0 0 0 / 6%) 0px 1px 2px, rgb(0 0 0 / 4%) 0px 2px 4px, rgb(0 0 0 / 2%) 0px 5px 14px;
}
.noPostMsgHeading {
    color: rgb(43, 43, 43);
    font-weight: 700;
    margin-bottom: 15px;
    font-size: 1.75rem;
}
.noPostMsgDisc {
    color: rgb(84, 84, 84);
    margin: 0px auto 10px;
    font-weight: 500;
    width: 80%;
    font-size: 14px;
}
.addBtn{
    border: 1px solid rgb(43, 43, 43);
    color: rgb(43, 43, 43);
    font-size: 0.80rem;
    text-transform: uppercase;
    padding: 0.65rem 1rem;
    border-radius: 5px;
    margin: 8px;
}
.addBtn:hover{
    color: #fff;
    background-color: #1a2029;
    border-color: #1a2029;
}
.showDemoPost {
    font-size: 14px;
    text-transform: uppercase;
    padding: 0.65rem 1rem;
    border-radius: 5px;
    margin: 8px;
    color: rgb(255, 255, 255);
}
.demoWallErrorStyle {
    box-shadow: rgb(0 0 0 / 30%) 0px 3px 10px;
    max-width: 100%;
    width: 100%;
    -webkit-transform: none;
            transform: none;
    top: 0px;
    z-index: 9998;
    position: fixed;
    padding: 10px;
    border-radius: 0px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgb(26, 32, 41);
}
strong {
    color: #ff4051;
    font-weight: 600
}
.text_primary{
    color: #ff4051;
}
.text_primary:hover{
    color: #e23442;
}
@media (max-width: 992px) {
    .demoWallErrorStyle{
        display: block;
    }
}
@media (max-width: 767px) {
    .noPostMsgDisc{
        width: 100%;
    }
}




.hideBtn {
    color: #fff;
    background-color: #ff4051;
    border: 1px solid transparent;
   font-weight: 500;
    font-size: 0.80rem;
    line-height: 1.5
}
.hideBtn:hover {
    background-color: #e23442;
    border-color: #e23442;
    color: #fff;
}

.erro_204_fix{
    box-shadow: rgba(0, 0, 0, 0.3) 0px 3px 10px; 
    max-width: 100%; 
    width: 100%; 
    -webkit-transform: none; 
            transform: none; 
    bottom: 0px; 
    z-index: 9998; 
    position: fixed; 
    padding: 15px; 
    border-radius: 0px; 
    text-align: center; 
    display: flex; 
    align-items: center; 
    justify-content: center; 
    background: rgb(26, 32, 41);
}

.noPostContent .erro_204_fix{
    box-shadow: none; 
    background: none;
    position: relative;

}


.erro_204_fix h2{ 
    font-size: 25px;
    font-weight: 600;
    margin: 0 15px 0px 0;
    color: #fff;
    text-transform: capitalize;
}

.erro_204_fix p{
    font-size: 16px;
    color: #fff;
    font-weight: 400;
    margin-bottom: 0px;
}

.erro_204_fix p a{
    color: #c20e5e;
    border-bottom: 1px dashed #c20e5e;
    transition: all ease .3s;
}
