  transition: opacity 300ms ease-in-out;
}

.tb_fade {
    transition: opacity 0.15s linear;
}

.tb_modal_dialog.show {
    opacity:1;
    visibility: visible;
}

.tb_modal_dialog {
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
    min-height: 100%;
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    padding: 0;
    margin:0 auto;
    pointer-events: none;
    max-width: 90vw;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    transition-property: transform,-webkit-transform,opacity;
    transition-duration: .2s;
    transition-timing-function: ease-in;
    transition: opacity .3s,-webkit-transform .3s ease;
    transition: opacity .3s,transform .3s ease;
    transition: opacity .3s,transform .3s ease,-webkit-transform .3s ease;
    -moz-transition: opacity .3s,transform .3s ease;
    -webkit-transition: opacity .3s,transform .3s ease;
    -o-transition: opacity .3s,transform .3s ease;
    -webkit-transform: scale(0.4);
    transform: scale(0.4);
}

.tb_modal.show .tb_modal_dialog{
    opacity: 1;
    visibility: visible;
    -webkit-transform: scale(1);
    transform: scale(1);
    transition: all 0.3s;
}

