.selected-item {
    position:absolute; 
    width:100%; 
    height:100%; 
    top:0; 
    left:0; 
    opacity:1; 
    z-index:1000;
    transition: all 1.2s ease-in-out 0.7s;
}
.selected-item .image{
    background-color:white; 
    position:absolute; 
    border:#f3f3f3 10px solid; 
    max-width:none; 
    box-shadow: black 0px 0px 30px;
    -webkit-box-shadow: black 0px 0px 30px;
    -moz-box-shadow: black 0px 0px 30px;
    opacity:0; 
    /* width:100%; 
     height:100%; */
    width:auto !important; height:auto !important;
    min-width:350px;
    max-width:350px;
    /*max-height:350px;*/
    overflow:hidden;
    transition: all 1.2s ease-in-out 0.7s;
}


.content-outer{ 
    top:0 !important; 
    /*left:0; */
    /*width:0px; */
    left:350px !important;
    position:absolute; 
    overflow:hidden;
    transition: all ease 1s;
}
.content-inner{ width:360px; padding:15px 20px 20px 20px; color:#444; background:#d0d0d1; background:rgba(208,208,208,.95);}
.content-inner .header{line-height:1.6em; margin-bottom:15px; margin-top:-10px;}
.content-inner .header .socicon{margin-left:.2em;}
.content-inner .header span{font-weight:bolder; font-size: 25px;}
.content-inner .header .header-subline{font-weight: normal; font-size: 20px; display:block;}
.content-inner .message-text{font-size:20px; word-wrap:break-word;}
.content-inner .date-text{font-size:18px; line-height:1em; margin-top:5px; margin-bottom:-5px; text-align:right; font-style:italic; word-wrap: break-word;}
.content-inner .rating{width:100%; text-align:center;}
.message-text a,
.message-text a:hover,
.message-text a:visited,
.message-text a:active{
    color:#444; 
    text-decoration:none;
    display: block;
    display: -webkit-box; 
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
.message-text a:hover{text-decoration:none;}
#tweet-list .tweet.show .selected-item{
    display:block;
    transition: all 1.0s ease-in-out 0s;
}

#tweet-list .tweet.show .image{
    opacity:1;
    transition: all ease 1s;
    width:auto !important; height:auto !important;
    min-width:350px;
    max-width:350px;
    /*max-height:350px;*/
}
#tweet-list .tweet .selected-item.tweetrPostSM .content-outer{ left:140px !Important; margin-top:100px;}
#tweet-list .tweet.show .selected-item.tweetrPostSM .content-outer{ left:140px !Important;}
#tweet-list .tweet.show .selected-item.tweetrPostSM .image{min-width:150px !important; max-width:150px !important}
#tweet-list .tweet .selected-item.tweetrPostSM .content-inner{margin-left:0px !important;}

#tweet-list .tweet .content-outer{
    position:absolute; top:0% !important; margin-top:200px !important; left:350px !important; visibility:hidden; opacity:0; 
    width:400px;
}
#tweet-list .tweet.show .content-outer{
    width:400px;
    margin-top:0px !important;
    left:350px !important;
    visibility:visible;
    opacity:1;
    transition: all 1.2s ease-in-out 0.7s;
}
#tweet-list .tweet:nth-child(4n) .image{
    -webkit-perspective: 1000;
    -o-perspective: 1000;
    perspective: 1000;
}
#tweet-list .tweet.show:nth-child(4n) .image{
    -webkit-transform: rotateY(360deg);
    transform: rotateY(360deg);
}
#tweet-list .tweet:nth-child(4n+1) .image{
    -webkit-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
}
#tweet-list .tweet.show:nth-child(4n+1) .image{
    -webkit-transform: rotateX(360deg);
    transform: rotateX(360deg);
}
#tweet-list .tweet:nth-child(4n+2) .image{
    -webkit-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
}
#tweet-list .tweet.show:nth-child(4n+2) .image{
    -webkit-transform: rotateX(360deg);
    transform: rotateX(360deg);
}
#tweet-list .tweet:nth-child(4n+3) .image{
    -webkit-perspective: 1000;
    -o-perspective: 1000;
    perspective: 1000;
}
#tweet-list .tweet.show:nth-child(4n+3) .image{
    -webkit-transform: rotateX(360deg);
    transform: rotateX(360deg);
}
#tweet-list .tweet .thumb{
    -webkit-perspective: 1000;
    -o-perspective: 1000;
    perspective: 1000;
    transition: all 1s ease-in-out;
}
#tweet-list .tweet.imageFlip .thumb{
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
    transition: all 0.1s ease-in-out;
}
 
#tweet-list .tweet .thumb img {
    width: 100%; 
    height: 100%; 
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    max-width: none;
    left: 50%;
}
