	}
#tweet-list .tweet[data-tweet="video"] .tweet-post-img{
	visibility:hidden;
}
#tweet-list .tweet[data-tweet="video"] .tweet-post-img.tweet-post-video{
	-webkit-perspective: 1000;
    -o-perspective: 1000;
    perspective: 1000;
	visibility:hidden;
 transition: all 1s ease-in-out;
	opacity:0 !Important;
	position:absolute;
	left:-150px;
	top:0;
	transform:scale(0.5, 0.5) rotateY(360deg);
	-webkit-transform:scale(0.5, 0.5) rotateY(360deg);
	-moz-transform:scale(0.5, 0.5) rotateY(360deg);
	-o-transform:scale(0.5, 0.5) rotateY(360deg);
	-ms-transform:scale(0.5, 0.5) rotateY(360deg);    
}
#tweet-list .tweet.show[data-tweet="video"] .tweet-post-img.tweet-post-video{
	visibility:visible; opacity:1 !Important;
	left:0;
	top:0;
	transform:scale(1, 1) rotateY(0deg);
	-webkit-transform:scale(1, 1) rotateY(0deg);
	-moz-transform:scale(1, 1) rotateY(0deg);
	-o-transform:scale(1, 1) rotateY(0deg);
	-ms-transform:scale(1, 1) rotateY(0deg);
}
