body {
	margin: 0px;
}
iframe{border:0; outline:0;}
/* Content Play Loader */
/* .loaderBox{ width:262px; position:absolute; left:50%; top:50%; 
      margin-left:-231px;
} */
.loaderBox {
          position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
    }
.logo{}
.progressDiv{ margin-top:10px;}
#overlayLoader{
    position:fixed;
    z-index:99999;
    top:0;
    left:0;
    bottom:0;
    right:0;
    background:rgba(0,0,0,1);
    transition: 1s 0.4s;
    -webkit-transition: 1s 0.4s;
}
.progressBlock
{
    background: #4d4d4d;
    height: 6px;
    width: 100px;
    margin: 0 auto;
    border-radius: 4px;
    -moz-border-radius: 4px;
    -webkit-border-radius: 4px;
    position: fixed;
    left: 0;
    right: 0;
}
.bar 
{ 
    background-color: #ffffff; 
    width:0%; 
    height:5px; 
    border-radius: 4px; 
    -moz-border-radius: 4px; 
    -webkit-border-radius: 4px; 
}
.percent 
{ 
    position:absolute; 
    display:inline-block; 
    top:3px; 
    left:48%; 
}
/* End Content Play Loader */

.mixheader { padding: 10px 35px; overflow: hidden}
.mixheader a.commanBtn{font-weight: 300; float:right; padding:6px 10px; background:none; border:1px solid #393838; box-shadow:inset 0px 1px 0px #000; -webkit-box-shadow:inset 0px 1px 0px #000; -moz-box-shadow:inset 0px 1px 0px #000;}
.mixheader a.commanBtn i{ margin:2px 6px 0 0; font-size: 14px;}
.mixheader a.commanBtn:hover{ background:#393838; box-shadow:none; -webkit-box-shadow:none; -moz-box-shadow:none;}
.mixTop{margin-top: 20px;}
.mixTop .displaymix{ 
    float: right;    
    color: #fff;
    text-transform: uppercase; 
    padding: 12px 30px;
    font:400 16px/16px "Lato";
}
.mixInnerdiv{
    margin: 15px 0; display:table; width:100%;
}
.mixInnerdiv .row{margin:0px; background:#fff; display:table-row;}
.mixInnerdiv .col-md-3{ 
    width:23%; 
    padding:0px; 
    display:table-cell; 
    float: none;
    border-left: 1px solid #e1e1e1;      
    border-top: 1px solid #e1e1e1; 
    position:relative;
	vertical-align:top;
}
.mixInnerdiv .col-md-9{padding:0px; width:77%; display:table-cell; float: none; border: 1px solid #d85262; vertical-align:top;}

.mixsidebar{
    background: #fcfcfc;
    margin-bottom: 55px;
}
.mixAddscene {
    display: block;
    padding: 7px 20px;
    font: 400 16px/30px "Open Sans";
    color: #fff;
    transition: all 300ms ease-in-out 0s;
    -webkit-transition: all 300ms ease-in-out 0s;
    background: #d85262;
    opacity: 1;
    position: absolute;
    bottom: 0px;
    width: 100%;
    text-transform:uppercase;
    z-index: 1;
	height: 43px;
}
.mixAddscene i{ font-size:18px; margin-right:5px;}
.mixAddscene img{
    float: right;
}
.mixAddscene:hover, .mixAddscene:visited, .mixAddscene:focus{
    color: #fff;
    text-decoration: none;
    opacity: 0.9;
}
.mixsidebarlist{
    padding: 0;
    margin: 0;
    list-style-type: none;
}
.mixsidebarlist li{
    padding: 10px;
    display: block;
    background: #ebebeb;
    margin-bottom:1px;
}
.mixsidebarlist li.active{
    background: #d85262;
}
.mixsidebarlist li.active *{ color:#fff !important;}
.mixsidebarlist li.active .sceneTime{ border-color:#cc525c; background:#d3525d;}
.mixsidebarlist .sceneTop h3{
    color: #333333;
    font:400 16px/16px "Lato";
    float: left;
    margin: 0;
