          transform-origin: 50% 0%;
  bottom: 0;
}

.tb_flip_00 .tb_flip_001 a div .inn {
  position: absolute;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 200%;
  color: #ccc;
  text-shadow: 0 1px 2px #000;
  text-align: center;
  background-color: #333;
  border-radius: 3px;
}
.tb_flip_00 .tb_flip_001 a .inn text {
  font-weight: bold;
  fill:#fff;
  font-size: 2em;
}

.wall_view .tb_flip_00 .tb_flip_001 a .inn text {
  font-size: 1.26em;
}

.tb_flip_00 .tb_flip_001 a div.up .inn {
  top: 0;
}

