* {
  box-sizing: border-box;
}

:root,
[data-theme=light] {
	--lb-light-rgb:255, 255, 255;
	--lb-dark-rgb:32, 37, 42;
	--lb-white-rgb:255, 255, 255;
	--lb-black-rgb:32, 37, 42;
	--lb-count-bg:#1A2029;
}

[data-theme=dark] {
	--lb-light-rgb:32, 37, 42;
	--lb-dark-rgb:255, 255, 255;
	--lb-white-rgb:255, 255, 255;
	--lb-black-rgb:32, 37, 42;
	--lb-count-bg:#ffffff0f;
}

body {
    font-family: 'Inter';
	margin: 0;
	padding: 0;
}

.ts_board_users{
	background-color: rgb(var(--lb-light-rgb));
	min-height: 100vh;
	width: 100%;
}
.ts_user_list_wrapper{
	width: 100%;
}

.ts_board_title {
    font-family: "Open Sans";
    color: rgb(var(--lb-dark-rgb));
	text-align: center;
    margin: 0;
	font-weight: 700;
    font-size: calc(13px + .3vw + .3vh);
    padding:calc(5px + .3vw + .3vh) calc(5px + .5vw + .5vh);
}

.ts_user_wrapper {
	position: relative;
	z-index: 1;
    /* min-height: 100vh; */
    display: flex;
    align-items: center;
    justify-content: center;
	flex-direction: column;
    padding-top: 10px;
}

.ts_user_list {
    flex: 1 0;
    padding:0 5px;
    display: flex;
    align-self: stretch;
}

.ts_author_wrap_in {
    width: 100%;
	background-color: rgb(var(--lb-white-rgb), 0.04);
    padding: calc(3vh) 10px;
}

.ts_author_wrapper {
    width: 100%;
	position: relative;
    overflow: hidden;
    border-radius: 8px;
	box-shadow: 0px 3px 30px rgba(15, 19, 39, 0.16);
    margin-bottom: 10px;
}

.ts_author_profile {
    display: flex;
    flex-shrink: 0;
	position: relative;
    padding-bottom: 60px;
	width: 60px;
    border-radius: 50%;
    margin: 0 auto;
	margin-bottom: 10px;
    background-color:  rgba(var(--lb-dark-rgb), .1);
} 

.ts_author_profile img{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    border-radius: 50%;
    border: 3px solid #fff;
}

.ts_author_info_wrapper {
    width: 100%;
	text-align: center;
    display: block;
    overflow: hidden;
}

.ts_authorname {
    color: rgb(var(--lb-dark-rgb));
    display: block;
    font-size: calc(8px + .25vw + .25vh);
    font-weight: 600;
    margin-bottom: 5px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}  
.ts_author_social_{
    text-align: center;
    position: absolute;
    bottom: 0px;
    right: 0px;
    background-color: #fff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 18px;
    width: 18px;
	box-shadow: 0px 0px 5px rgba(15, 19, 39, 0.16);
}

.ts_social_ico{
    margin: 0;
    font-size: 13px;
    line-height: 18px;
    text-align: center;
}

.ts_author_social_right{
    display: none;
}

.ts_username, .ts_followers_count {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: rgb(var(--lb-dark-rgb));
    font-size: 12px;
	margin-bottom: 5px;
	display: block;
}

.ts_author_count_wrapper {
    position: absolute;
    right: 0;
    top: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 2;
    width: calc(10px + 1vh + 1vw);
    height: calc(10px + 1vh + 1vw);
}
.ts_author_count_wrapper:before {
	content: '';
    position: absolute;
    right: 0;
    top: 0;
    border-top-style: solid;
    border-top-color: var(--lb-count-bg);
    border-left-style: solid;
    border-left-color: transparent;
    border-top-width: calc(30px + 1vh + 1vw);
    border-left-width:calc(30px + 1vh + 1vw);
}

.tb_author_count{
	position: relative;
	z-index: 1;
    color: #fff;
    font-weight: 700;
    font-size: calc(5px + .3vh + .3vw);
}

.ts_top_users {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1 0;
}

.ts_all_users {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    flex-grow: 1;
}

.ts_user_list_wrapper {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}


.ts_users_top_3 .ts_all_users,
.ts_users_top_5 .ts_all_users{
    display: none;
}






.ts_top_users .ts_user_list {
    flex: 1 0;
    padding: 0 calc(10px * 2);
}

.ts_top_users .ts_author_wrapper{
    margin-bottom: calc(10px * 2);
}

.ts_top_users .ts_author_wrap_in{
    /* padding: calc(10px + 5vh) calc(15px); */
}
.ts_top_users .ts_author_profile {
    padding-bottom: calc(60px * 1.2);
    width: calc(60px * 1.2);
} 
.ts_top_users .ts_authorname, 
.ts_top_users .ts_username{
    margin-bottom: calc(5px + 0.5vh);
}

.ts_top_users .ts_author_profile{
    margin-bottom: calc(10px + 2vh);
}

.ts_top_users .ts_authorname {
    font-size: calc(10px + .25vw + .25vh);
}

.ts_top_users .ts_username,
.ts_top_users .ts_followers_count{
    font-size: 14px;
}

.ts_top_users .ts_author_count_wrapper:before {
    border-top-width: 60px;
    border-left-width: 60px;
}

.ts_top_users .ts_author_count_wrapper {
    width: calc((10px + 1vh + 1vw) * 1.2);
    height: calc((10px + 1vh + 1vw) * 1.2);
}
.ts_top_users .ts_author_count_wrapper:before {
    border-top-width: calc((30px + 1vh + 1vw) * 1.2);
    border-left-width:calc((30px + 1vh + 1vw) * 1.2);
}
.ts_top_users .tb_author_count{
    font-size: calc((5px + .3vh + .3vw) * 1.2);
}

.ts_top_users .ts_author_social_{
    height: calc(18px * 1.4);
    width: calc(18px * 1.4);
}

.ts_top_users .ts_social_ico{
    font-size: calc(13px * 1.4);
    line-height: calc(18px * 1.4);
}

.ts_users_top_3 .ts_top_users{
    max-width: 70rem;
    width: 100%;
}

.ts_users_top_5 .ts_top_users{
    max-width: 100%;
    width: 100%;
