}
.image_upload_box_content h3{
    margin-bottom: 0px;
    margin-top: 10px;
    color: #2B2B2B;
}
.image_upload_box_content p{
    margin-top: 10px;
    margin-bottom: 0px;
    color: #545454;
}
.submit_pic_uploader_status{
    position: absolute;
    bottom: 0px;
    width: 100%;
}
.add-text{
    margin-bottom: 15px;
}
.add-text input.input-fileld{
    width: 100%;
    border: 1px solid #E9ECED !important;
    padding: 15px;
