}
.image_upload_box_content .uppy-DragDrop-inner::before{
    content: '';
    display: block;
    background: url(data:image/svg+xml;base64,);
    height: 22px;
    width: 25px;
    background-size: contain;
    background-repeat: no-repeat;
    margin: 0 auto;
    margin-bottom: 10px;
}
.uppy-DragDrop-container:focus{
    box-shadow: none !important;
}
.image_upload_box_content .uppy-DragDrop-inner svg{
    display: none;
}
.error-text{
    font-size: 12px;
    color: #ff4051;
}
.submit-error{
    margin-bottom: 10px;
}
.following_information .terms_condition{
    margin-top: 0px;
    margin-bottom: 20px;
    font-size: 14px;
}

.btn-primary.submit_pic_spinner{
    padding-right: 3rem;
    opacity: 0.6;
    box-shadow: none;
    pointer-events: none;
}

.submit_pic_spinner:after {
    content: "";
    box-sizing: border-box;
    width: 1.4rem !important;
    display: inline-block;
    height: 1.4rem;
    vertical-align: middle;
    border-radius: 50%;
    border: 2px solid #ffffff;
    border-right: 2px solid transparent;
    -webkit-animation: submit_pic_animation-spinner 0.5s linear infinite;
            animation: submit_pic_animation-spinner 0.5s linear infinite;
    margin-left: 5px;
}

@-webkit-keyframes submit_pic_animation-spinner {
    to {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
}
  
@keyframes submit_pic_animation-spinner {
    to {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
}


.btn-primary:not(.submit_pic_disabled):hover {
    background-color: #000;
}
.submit_pic_disabled{
    opacity: 0.6;
    box-shadow: none;
}
.submit_pic_limit_error{
    width: 100%;
    text-align: center;
    justify-content: center;
    margin-bottom: 10px;
}


