        font-size: 14px;
    }
    .submit_post_tabs .saprator{
        font-size: 12px;
    }
    .post_tabs_content,
    .submit-post-main {
        padding: 15px;
    }
    .image_upload_box_content h3{
        font-size: 14px;
    }
