.commanBtn {
    background: #c83b77;
    background: -moz-linear-gradient(45deg, #c83b77 0, #e06a73 100%);
    background: -webkit-linear-gradient(45deg, #c83b77 0, #e06a73 100%);
    background: linear-gradient(45deg, #c83b77 0, #e06a73 100%);
    color: #fff;
    font: 400 14px/18px 'open sans', sans-serif;
    margin-right: 3px;
    display: inline-block;
    vertical-align: middle;
    transition: all ease .3s;
    -moz-transition: all ease .3s;
    -webkit-transition: all ease .3s;
    cursor: pointer;
    border-radius: 7px;
    -webkit-border-radius: 7px;
    -moz-border-radius: 7px;
    text-decoration: none;
    text-transform: uppercase
}

.commanBtn:focus, .commanBtn:hover {
    background: #e06a73;
    background: -moz-linear-gradient(left, #e06a73 17%, #c83b77 90%);
    background: -webkit-linear-gradient(left, #e06a73 17%, #c83b77 90%);
    background: linear-gradient(to right, #e06a73 17%, #c83b77 90%)
}

.commanBtn:focus {
    margin-top: 2px
}

.commanBtn.btn-sm {
    padding: 8px 12px
}

.commanBtn.btn-md {
    padding: 13px 25px;
    font: 400 14px/18px 'open sans', sans-serif
}

.commanBtn.btn-sm.backBtn:active, .commanBtn.btn-sm.backBtn:visited {
    transform: scale(.96)
}

.commanBtn.secondryBtn {
    background: #e9e8e9;
    background: -moz-linear-gradient(90deg, #e9e8e9 0, #fff 100%);
    background: -webkit-linear-gradient(90deg, #e9e8e9 0, #fff 100%);
    background: linear-gradient(90deg, #e9e8e9 0, #fff 100%);
    color: #333;
    border: 1px solid #e9e8e9;
    border-radius: 4px
}

.commanBtn.secondryBtn:focus, .commanBtn.secondryBtn:hover {
    background: #fff;
    background: -moz-linear-gradient(90deg, #fff 0, #e9e8e9 100%);
    background: -webkit-linear-gradient(90deg, #fff 0, #e9e8e9 100%);
    background: linear-gradient(90deg, #fff 0, #e9e8e9 100%);
    color: #333
}

a.commanBtn.resetBtn {
    position: relative;
    margin-right: 0;
    color: #c83b77
}

a.commanBtn.resetBtn::before {
    content: '';
    background: #fff;
    position: absolute;
    left: 1px;
    bottom: 1px;
    top: 1px;
    right: 1px;
    border-radius: 7px
}

a.commanBtn.resetBtn span {
    position: relative
}

a.commanBtn.resetBtn:hover {
    color: #fff
}

a.commanBtn.resetBtn:hover::before {
    background: 0 0
}

