.mashup__ {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}
.tb_mash_grid {
    position: absolute;
    transition: all 1s linear;
}

.tb_mash_grid.tb_grid_22 {
    width: 66.666667%;
    left: 0;
    top: 0;
    height: 100%;
    z-index: 1;
}

.tb_mash_grid.tb_grid_23 {
    width: 66.666667%;
    left: 0;
    top: 0;
    height: 100%;
    z-index: 3;
}

.tb_mash_grid.tb_grid_13 {
    width: 33.333333%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
}

.tb_mash_grid.tb_grid_13 {
    left: 66.666667%;
}

.tb_01_post_36__ {
    width: 100%;
    padding-bottom: 50vh;
    position: relative;
    background-color: #000;
    overflow: hidden;
}

.tb_grid_22 .tb_01_post_36__ {
    padding-bottom: 100vh;
}

.tb_slide_up .tb_01_post_36__:last-child .tb_media_img_002{
    animation: TbFromCenter 40s infinite;
    -webkit-animation: TbFromCenter 40s infinite;
    -moz-animation: TbFromCenter 40s infinite;
    -ms-animation: TbFromCenter 40s infinite;
}

.tb_media_img_002 iframe {
    height: 100%;
}
@-webkit-keyframes TbFromCenter {
    from {
        -webkit-transform: translate(0, 50%);
        transform: translate(0, 50%);
        visibility: visible;
    }

    to {
        -webkit-transform: translate(0, -50%);
        transform: translate(0, -50%);
    }
}
@keyframes TbFromCenter {
    from {
        -webkit-transform: translate(0, 50%);
        transform: translate(0, 50%);
        visibility: visible;
    }

    to {
        -webkit-transform: translate(0, -50%);
        transform: translate(0, -50%);
    }
}


.tb_slide_up .tb_01_post_36__:last-child img {
    width: 100%;
    max-height: initial;
}

