@-webkit-keyframes TbslideInUp {
    from {
      -webkit-transform: translate3d(0, 100%, 0);
      transform: translate3d(0, 100%, 0);
      visibility: visible;
    }
  
    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
}
@keyframes TbslideInUp {
    from {
      -webkit-transform: translate3d(0, 100%, 0);
      transform: translate3d(0, 100%, 0);
      visibility: visible;
    }
  
    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
}

.tb_animate__slideInUp {
    -webkit-animation-name: TbslideInUp;
    animation-name: TbslideInUp;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

@-webkit-keyframes TbslideInDown {
    from {
      -webkit-transform: translate3d(0, -100%, 0);
      transform: translate3d(0, -100%, 0);
      visibility: visible;
    }
  
    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
}
@keyframes TbslideInDown {
    from {
      -webkit-transform: translate3d(0, -100%, 0);
      transform: translate3d(0, -100%, 0);
      visibility: visible;
    }
  
    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
}

.tb_animate__slideInDown {
    -webkit-animation-name: TbslideInDown;
    animation-name: TbslideInDown;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}


.tb_mash_grid.tb_grid_13.tb_slideRight {
    left: 100%;
}
.tb_mash_grid.tb_grid_23.tb_slideRight {
    left: 33.333333%;
}


.slideShiftRight {
    -webkit-animation: slideright 1s forwards;
    animation: slideright 1s forwards;
    left: 33.333333%;
}
.slideShiftLeft3 {
    animation: slideLeft3 2s forwards;
    -webkit-animation: slideLeft3 2s forwards;
    -moz-animation: slideLeft3 2s forwards;
    -ms-animation: slideLeft3 2s forwards;
    left: 0%;
    z-index: 500;
}


.tb_01_no_post_001 {
    max-width: 30%;
    display: block;
    width: 120px;
}

.tb_01_media_36__ {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    opacity: 1;
    visibility: visible;
}

.tb_01_blur {
    height: 100%;
    width: 100%;
    background-size: cover;
    filter: blur(20px);
    filter: blur(30px) saturate(2);
    -webkit-filter: blur(30px) saturate(2);
    -moz-filter: blur(30px) saturate(2);
    -o-filter: blur(30px) saturate(2);
    -ms-filter: blur(30px) saturate(2);
    opacity: 0.7;
    position: relative;
}

.tb_01_blur:after {
    content: '';
    display: block;
    background: rgba(0, 0, 0, 0.3);
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    height: 100%;
    width: 100%;
    z-index: 1;
}

.tb_media_img_002 {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
}

.tb_media_img_002 img {
    max-width: 100%;
    max-height: 100%;
    margin: 0 auto;
    position: absolute;
    z-index: 1;
    left: 0;
    right: 0;
}


.tb_slideFull .tb_media_img_002 img{
    animation: imgmove 40s infinite;
    -webkit-animation: imgmove 40s infinite;
    -moz-animation: imgmove 40s infinite;
    -ms-animation: imgmove 40s infinite;
    top: 0%;
   
}

@-webkit-keyframes imgmove {
    from { top: 50%; }
    to{ top: -100%;}
}

@keyframes imgmove {
    from { top: 50%; }
    to{ top: -100%;}
}


.tb_post_content_wrap_001{
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    z-index: 3;
    display: flex;
    flex-direction: column;
}

.tb_post_content_wrap_001 .rating img, .tb_slide_up .tb_01_post_36__:last-child .rating img {
    max-height: 20px;
    width: auto;
    max-width: 100%;
    margin-bottom: 5px;
}

.tb_post_cont_001 {
    padding: 10px 15px;
}
.tb_cont_txt_001 {
    font-size: 16px;
    color: #fff;
    text-align: left;
    word-break: break-word;
}

.tb_post_author_wrap_001 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 15px;
}

.tb_author_profile_ {
    width: 44px;
    height: 44px;
    border-radius: 50%;
    border: 3px solid #fff;
    margin-right: 10px;
    overflow: hidden;
}

.tb_post_author_001 {
    display: flex;
    align-items: center;
    justify-content: start;
    width: 95%;
}

.tb_author_wrap {
    -ms-flex-negative: 1;
    flex-shrink: 1;
}

.tb_author_details_001 {
    display: flex;
    align-items: start;
    flex-direction: column;
    -ms-flex-positive: 1;
    flex-grow: 1;
}

.tb_author_name_ {
    font-size: 18px;
    color: #fff;
    font-weight: 600;
}

.tb_author_details_01 {
    display: flex;
    align-items: center;
    width: 100%;
}

.tb_author_username, .tb_post_time_ {
    color: #fff;
    font-size: 12px;
    white-space: nowrap;
    overflow: hidden;
    max-width: 40%;
    text-overflow: ellipsis;
}

.tb_sepratedot_ {
    height: 4px;
    width: 4px;
    background-color: #fff;
    border-radius: 50px;
    margin: 0 6px;
}

.tb_post_social_icon .fa {
    /* color: #fff; */
    font-size: 28px;
}
.tb_media_img_002:after{
    content: "";
    background: -webkit-linear-gradient(top, rgba(115, 115, 115, 0) 0%, rgba(91, 91, 91, 0.2) 70%, rgba(0,0,0,0.9) 100%);
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0px;
    width: 100%;
    height: 100%;
}