/**Modern Waterfall**/
.themeTickerWall .hashtag{ width:250px; float:left; height:inherit; line-height:100%; background-color:#333; text-align:center; position:relative;}
.themeTickerWall .hashtag h1{ line-height:100%; margin:0; position:absolute; left:50%; top:50%; transform:translate(-50%,-50%); -webkit-transform:translate(-50%,-50%); -moz-transform:translate(-50%,-50%); font-weight:700; font-size:26px;color:#fff;} 
.themeTickerWall .postedTxt.setFont{margin: 0;width: 800px;font-size:15px;color:#888;margin-top:0px;right: 30px;transform: translateY(-50%);top: 50%;position: absolute;}
.themeTickerWall .postsSectionWrap{ width: calc(100% - 250px); float:left; height:inherit; background-color:#fff; overflow:hidden;}	
.themeTickerWall .postsSection{ height:100%;}
.themeTickerWall .postContentCard{
	padding: 0 30px 0 30px;
	height: 100%;
	position: relative;
}	
.item {
    float: none;
    display: inline-block;
}	
.themeTickerWall .flatCard{
	width:1180px; 
	margin:0px; 
	padding:0px; 
	height:100%; 
	position:relative; 
	margin-left: -5px;
}

.themeTickerWall .flatCard p{white-space:normal;}
.themeTickerWall .flatCard:after{
	content:''; position:absolute; width:1px; display:block; border-right:1px solid #ccc; right:2px; height:60%; top:20%;
}
.themeTickerWall .flatCard .authrHandleTime{
	 overflow: hidden;
}
.themeTickerWall .flatCard .sepratedot{
	color:#fff;
}	
.themeTickerWall .flatCard h4{
	float:left;
	width:285px;
	margin:0px;
	transform: translateY(-50%);
	top: 50%;
	position: absolute;
}

.themeTickerWall .authorDetails .authrHandle { 
	max-width: 90px;
}

.themeTickerWall .authorDetails img {
    height: 41px;
    width: 41px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    float: left;
    margin-right: 10px;
}

.themeTickerWall .authorDetails .postAuthorName {
    font: 600 13px/18px 'open sans';
    white-space: nowrap;
    text-decoration: none;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
}

.themeTickerWall .flatCard h4 .postNetwork {
    margin-top: 8px;
    float: right;
    margin-left: 20px;
}
.themeTickerWall .flatCard h4 .postNetwork i {
    font-size: 24px;
    line-height: 18px;
}

.themeTickerWall {
    width: 100%;
    position: relative;
    height: 100%;
    max-height: 200px;
    /* height: 100px; */
    box-shadow: 0 0 1px #f1f1f1;
    background-color: #fff;
}

.themeTickerWall #sPosts {
    white-space: nowrap;
    margin-bottom: 0;
}

.themeStart51{
height: 100vh;
}

.themeStart51 .bannerTop{
    display: none;
}

.themeStart51 .headerTicker{
    display: none;
}