.themeStart56 .flatCard .post {

    position: relative;
    border: none !important;
    overflow: hidden;
    border-radius: 6px;
    box-shadow: 2;
    box-shadow: 0 1px 2px rgb(0 0 0 / 6%), 0 2px 4px rgb(0 0 0 / 4%), 0 5px 14px rgb(0 0 0 / 2%);
}

.themeStart56 .flatCard .postContentCard {
    padding: 15px;
}


.themeStart56 .postContentCard h4 {
    display: table;
    width: 100%;
    margin-top: 15px;
}

.themeStart56 .flatCard .post1{
    background-color: #139dde !important;
}

.themeStart56 .flatCard .post2{
    background-color: #ff0000!important;
}

.themeStart56 .flatCard .post3{
    background-color: #0c65c2 !important; 
}

.themeStart56 .flatCard .post4{
    background-color: #f07800 !important;
}
