.mqtd_quote_card {
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.mqtd_quote_overlay {
  background: #0000005c;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  position: absolute;
}
.mqtd_quoteCardContent {
  padding: 5px;
  max-width: 100%;
  /* margin: auto; */
  position: relative;
  border-radius: 15px;
  color: #fff;
  height: 100%;
  width:100%;
}
