.mqtd_quote_animation {
  animation: fadeIn ease 5s;
  -webkit-animation: fadeIn ease 5s;
  -moz-animation: fadeIn ease 5s;
  -o-animation: fadeIn ease 5s;
  -ms-animation: fadeIn ease 5s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.mqtd_quote_animation.mqtd_time_delay_one {
  -webkit-animation-delay: 1.3s;
  animation-delay: 1.3s;
}
.mqtd_quote_animation.mqtd_time_delay_two {
  -webkit-animation-delay: 2.3s;
  animation-delay: 2.3s;
}
.mqtd_quote_animation.mqtd_time_delay_three {
  -webkit-animation-delay: 3.3s;
  animation-delay: 3.3s;
}
.mqtd_quote_animation.mqtd_time_delay_four {
  -webkit-animation-delay: 4.3s;
  animation-delay: 4.3s;
}

@media only screen and (max-width: 678px) {
  .mqtd_quote_authername {
    font-size: 12px;
  }
  .mqtd_quote_text {
    font-size: 18px;
  }
  /* .mqtd_quote_category {
      font-size: 12px;
    } */
}




.mqtd_quote_icon.toggleIn {
  animation: quoteAndAuthorEntrance .8s forwards;
  /* animation-delay: .8s; */
}


.mqtd_quote_text.toggleIn {
  animation: quoteAndAuthorEntrance .8s forwards;
 /* animation-delay: .8s; */
}
.mqtd_quote_authername.toggleIn {
  animation: quoteAndAuthorEntrance .8s forwards;
    /* animation-delay: .8s; */
}

.mqtd_quote_card.toggleOut {
  animation: backgroundExit .8s forwards;
  /* animation-delay: .8s; */
}

@keyframes backgroundExit {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(1.05);
  }
}

.mqtd_quote_card.toggleIn {
  animation: backgroundEntrance .8s forwards;
}

@keyframes backgroundEntrance {
  from {
    opacity: 0;
    transform: scale(1.05);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}


@keyframes quoteAndAuthorEntrance {
  from {
    color: rgba(255, 255, 255, 0);
    transform: translateY(5px);
  }
  to {
    color: rgba(255, 255, 255, 1);
    transform: translateY(0px);
  }
}

.mqtd_quote_icon.toggleOut {
  animation: quoteAndAuthorExit .8s forwards;
}
.mqtd_quote_text.toggleOut {
  animation: quoteAndAuthorExit .8s forwards;
}
.mqtd_quote_authername.toggleOut {
  animation: quoteAndAuthorExit .8s forwards;
}

@keyframes quoteAndAuthorExit {
  from {
    color: rgba(255, 255, 255, 1);
    transform: translateY(0px);
  }
  to {
    color: rgba(255, 255, 255, 0);
    transform: translateY(5px);
  }
}





.mqtd_quote_card.toggleIn {
  animation: backgroundEntrance .8s forwards;
}
@keyframes backgroundEntrance {
  from {
    opacity: 0;
    transform: scale(1.05);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.mqtd_quote_card .zoomOut {
  animation: backgroundZoomout 2s forwards;
  -webkit-animation-delay: .8s;
  animation-delay: .8s;
  transform: scale(0);
}
@keyframes backgroundZoomout {
  0% {
    opacity: 0;
    transform: scale(0.3);
  }
  50% {
    opacity: 0.8;
    transform: scale(1.05);
  }
  100% {
    opacity: 1;
    transform: inherit;
  }
}
