.tb_01_post_001__ {
    width: 25vw;
    position: relative;
    float: left;
}

.tb_01_post_wrap_{
    position: relative;
    height: 100%;
    width: 100%;
    overflow: hidden;
}
.tb_01_no_post_001 {
    max-width: 30%;
    display: block;
    width: 120px;
}

.tb_01_media_001__ {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    visibility: hidden;
}

.tb_01_post_img_wrapper_001,
.tb_01_post_vid_001__ {
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.tb_01_vid_play_icon__ {
    max-width: 50px;
    width: 20%;
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    z-index: 1;
}

.tb_01_play_icon001 {
    max-width: 100%;
    display: block;
}
.tb_01_post_vid_001__ iframe {
    width: 100%;
    height: 100%;
}
.tb_01_post_img_001 {
    background-size: cover;
    height: 100%;
    width: 100%;
    background-repeat: no-repeat;
    background-position: center;
}

.tb_01_post_img_in_002 {
    margin: 0 auto;
    display: block;
    width: 100%;
}

.tb_01_post_img_wrapper_001_blur_bg {
    position: absolute;
    width: 100%;
    height: 100%;
    background-size: cover;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    filter: blur(30px) saturate(2);
    -moz-filter: blur(30px) saturate(2);
    -webkit-filter: blur(30px) saturate(2);
    -ms-filter: blur(30px) saturate(2);
    -o-filter: blur(30px) saturate(2);
    opacity: 0.7;
}

.tb_01_post_img_in_002_blur_bg{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    filter: blur(30px) saturate(2);
    -moz-filter: blur(30px) saturate(2);
    -webkit-filter: blur(30px) saturate(2);
    -ms-filter: blur(30px) saturate(2);
    -o-filter: blur(30px) saturate(2);
    opacity: 0.7;
}

.tb_01_no_post_001 {
    max-width: 30%;
    display: block;
    width: 100px;
    margin: 0 auto;
}

.tb_01_post_icon__003 {
    position: absolute;
    right: 10px;
    bottom: 10px;
    z-index: 5;
}

.tb_01_post_icon__003 .fa {
    font-size: 22px;

}

.tb_01_post_video__ {
    margin: 0 auto;
    width: 100%;
    height: 100%;
    /* object-fit: cover; */
    transform: scale(1.005);
}

.tb_01_media_001__.tb_show_ {
    opacity: 1;
    visibility: visible;
    transform: scale(1, 1);
    -moz-transform: scale(1, 1);
    -webkit-transform: scale(1, 1);
    animation: zoomIn 0.3s forwards;
    -webkit-animation: zoomIn 0.3s forwards;
    -moz-animation: zoomIn 0.3s forwards;
    -ms-animation: zoomIn 0.3s forwards;
}

@-webkit-keyframes zoomIn {
    0% {
        transform: scale(0.2, 0.2);
        -moz-transform: scale(0.2, 0.2);
        -webkit-transform: scale(0.2, 0.2);
    }

    100% {
        transform: scale(1, 1);
        -moz-transform: scale(1, 1);
        -webkit-transform: scale(1, 1);
    }
}

@keyframes zoomIn {
    0% {
        transform: scale(0.2, 0.2);
        -moz-transform: scale(0.2, 0.2);
        -webkit-transform: scale(0.2, 0.2);
    }

    100% {
        transform: scale(1, 1);
        -moz-transform: scale(1, 1);
        -webkit-transform: scale(1, 1);
    }
}