.nbtd_view_wall {
  width: 80%;
  margin: auto;
}
.nbtd_wall_wraper {
  position: relative;
  display: block;
  content: "";
  width: 100%;
  padding-top: 56.25%;
}
.nbtd_noticboard_card {
  border: 6px solid #2b2b2b;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 2%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.nbtd_logo_icon {
  display: flex;
}
.nbtd_logo_icon_img {
    /* max-width: 50px; */
  max-height: 50px;
}
.notice_preview{
    padding: 3%;
}
