.themeStart46 .flatCard{overflow:visible;}
.themeStart46 .flatCard .post{ 
	box-shadow:5px 5px 18px -6px rgba(0, 0, 0, 0.3);
	border:0px;
	border-radius: 10px;
}
.themeStart46 .flatCard .postContentCard {
    padding: 26px 18px;
}
.themeStart46 .flatCard .image + .postContentCard {padding-top:0;}
.themeStart46 .flatCard .authorDetails img {
    height: 48px;
    width: 48px; 
	margin-top: -5px;
	margin-right:8px;
}

.themeStart46 .authorDetails .postAuthorName {
    font: 600 16px/24px 'open sans';	
}
.themeStart46 .authorDetails .authrHandle, .themeStart46 .authorDetails .timePost{
	font: 400 14px/24px 'open sans';
	color:#707070;
}
.themeStart46 .authorDetails .authrHandle{ max-width:80px;}
.themeStart46 .sepratedot {
    color: #707070;
    margin: 4px 4px 0 5px;
	font-size:5px;
}

.themeStart46 .flatCard h4 .postNetwork {
    margin-top: 0px;
    float: none;
    border-bottom: 60px solid #cd486b;
    border-left: 60px solid transparent;
    position: absolute;
    left: auto;
    right: 0px;
    top: auto;
    bottom: 0;
	margin:0;
}
.themeStart46 .flatCard h4 .postNetwork i{
	bottom: -50px;
	top: auto;
	right: 8px;
	position: absolute;
	font-size: 18px;
	color:#fff !Important;
}
.themeStart46 .flatCard .postedTxt p, .themeStart46 .flatCard .postedTxt h1 {
    font-size: inherit;
	
	color:inherit;
}

.themeStart46 .flatCard .postedTxt { 
    margin: 20px 0px 10px 0;
}
.themeStart46 .postContent div.image{
	margin:0px;
    overflow: hidden;
}
.themeStart46 .flatCard h4{
margin-top: 10px;
}
.themeStart46 .flatCard.onlyTextCard .postedTxt{
	margin-bottom:0px;
	position:static;
	transform:none;
	-webkit-transform:none;
	-moz-transform:none;
	-ms-transform:none;
	text-align:left;
}
.themeStart46 .flatCard.onlyTextCard .postedTxt.decorateTxt{margin:40px 0; text-align:center;}
.themeStart46 .flatCard.onlyTextCard .postContent p{ padding:0;}
.themeStart46 .flatCard.onlyTextCard h4 .authorDetails{position:relative; bottom:auto; width:100%;}
.themeStart46 .flatCard.onlyTextCard h4 .postNetwork{
	transform:none;
	-webkit-transform:none;
	-moz-transform:none;
	-ms-transform:none;
}

.cardPreview .themeStart46 .flatCard .post{
	border-radius:10px; 
	-moz-border-radius:10px; 
	-webkit-border-radius:10px; 
	box-shadow:5px 5px 18px -6px rgba(0, 0, 0, 0.3);
	-webkit-box-shadow:5px 5px 18px -6px rgba(0, 0, 0, 0.3);
	-moz-box-shadow:5px 5px 18px -6px rgba(0, 0, 0, 0.3);
}
.cardPreview .themeStart46 .authorDetails .authrHandle{ max-width:100px; }
