.hashtag{
    position:absolute; 
    top:10px; 
    left:10px; 
    color:#333; 
    font-size:1.250em; 
    font-size:1.250em; 
    background-color:rgba(255,255,255,0.8); 
    padding:5px 10px;  
    z-index:1100;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
}
.hashtag span{ 
    font-size:1.5em; 
    color:#FFF;
}
.hashtag img{ 
    width:1.4em;
}
.grid_overlay{
    position:absolute; 
    top:0; left:0; 
    width:3000px; 
    height:3000px; 
    background-color:#220924; 
    z-index:40; 
    opacity:.5;
    -webkit-transition: background-color 2s ease-in-out;
    -moz-transition: background-color 2s ease-in-out;
    -o-transition: background-color 2s ease-in-out;
    transition: background-color 2s ease-in-out;
}
.grid_overlay.bg1{ background-color:rgb(73, 25, 18)}
.grid_overlay.bg2{ background-color:rgb(69, 18, 73)}
.grid_overlay.bg3{ background-color:rgb(18, 64, 73)}
.grid_overlay.bg4{ background-color:rgb(34, 9, 36)}

