.selected-item {
    position:absolute; 
    width:100%; 
    height:100%; 
    top:0; 
    left:0; 
    opacity:1; 
    z-index:1000;
    -webkit-transition: all 1.2s ease-in-out 0.7s;
    -moz-transition: all 1.2s ease-in-out 0.7s;
    -o-transition: all 1.2s ease-in-out 0.7s;
    transition: all 1.2s ease-in-out 0.7s;
}
.selected-item .image{
    background-color:white; 
    position:absolute; 
    border:#f3f3f3 10px solid; 
    max-width:none; 
    box-shadow: black 0px 0px 30px;
    -webkit-box-shadow: black 0px 0px 30px;
    -moz-box-shadow: black 0px 0px 30px;
    opacity:0; 
    /* width:100%; 
     height:100%; */
    width:auto !important; height:auto !important;
    min-width:350px;
    max-width:350px;
    /*max-height:350px;*/
    overflow:hidden;
    -webkit-transition: all 1.2s ease-in-out 0.7s;
    -moz-transition: all 1.2s ease-in-out 0.7s;
    -o-transition: all 1.2s ease-in-out 0.7s;
    transition: all 1.2s ease-in-out 0.7s;
}


.content-outer{ 
    top:0 !important; 
    /*left:0; */
    /*width:0px; */
    left:350px !important;
    position:absolute; 
    overflow:hidden;
    -webkit-transition: all ease 1s;
    -moz-transition: all ease 1s;
    -o-transition: all ease 1s;
    transition: all ease 1s;
}
.content-inner{ width:360px; padding:15px 20px 20px 20px; color:#444; background:#d0d0d1; background:rgba(208,208,208,.95);}
.content-inner .header{line-height:1.6em; margin-bottom:15px; margin-top:-10px;}
.content-inner .header .socicon{margin-left:.2em;}
.content-inner .header span{font-weight:bolder; font-size: 25px;}
.content-inner .header .header-subline{font-weight: normal; font-size: 20px; display:block;}
.content-inner .message-text{font-size:20px; word-wrap:break-word;}
.content-inner .date-text{font-size:18px; line-height:1em; margin-top:5px; margin-bottom:-5px; text-align:right; font-style:italic; word-wrap: break-word;}
.content-inner .rating{width:100%; text-align:center;}
.message-text a,
.message-text a:hover,
.message-text a:visited,
.message-text a:active{
    color:#444; 
    text-decoration:none;
    display: block;
    display: -webkit-box; 
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
.message-text a:hover{text-decoration:none;}
#tweet-list .tweet.show .selected-item{
    display:block;
    -webkit-transition: all 1.0s ease-in-out 0s;
    -moz-transition: all 1.0s ease-in-out 0s;
    -o-transition: all 1.0s ease-in-out 0s;
    transition: all 1.0s ease-in-out 0s;
}

#tweet-list .tweet.show .image{
    opacity:1;
    -webkit-transition: all ease 1s;
    -moz-transition: all ease 1s;
    -o-transition: all ease 1s;
    transition: all ease 1s;
    width:auto !important; height:auto !important;
    min-width:350px;
    max-width:350px;
    /*max-height:350px;*/
}
#tweet-list .tweet .selected-item.tweetrPostSM .content-outer{ left:140px !Important; margin-top:100px;}
#tweet-list .tweet.show .selected-item.tweetrPostSM .content-outer{ left:140px !Important;}
#tweet-list .tweet.show .selected-item.tweetrPostSM .image{min-width:150px !important; max-width:150px !important}
#tweet-list .tweet .selected-item.tweetrPostSM .content-inner{margin-left:0px !important;}

#tweet-list .tweet .content-outer{
    position:absolute; top:0% !important; margin-top:200px !important; left:350px !important; visibility:hidden; opacity:0; 
    width:400px;
}
#tweet-list .tweet.show .content-outer{
    width:400px;
    margin-top:0px !important;
    left:350px !important;
    visibility:visible;
    opacity:1;
    -webkit-transition: all 1.2s ease-in-out 0.7s;
    -moz-transition: all 1.2s ease-in-out 0.7s;
    -o-transition: all 1.2s ease-in-out 0.73s;
    transition: all 1.2s ease-in-out 0.7s;
}
#tweet-list .tweet:nth-child(4n) .image{
    -webkit-perspective: 1000;
    -moz-perspective: 1000;
    -o-perspective: 1000;
    perspective: 1000;
}
#tweet-list .tweet.show:nth-child(4n) .image{
    -webkit-transform: rotateY(360deg);
    -moz-transform: rotateY(360deg);
    -o-transform: rotateY(360deg);
    transform: rotateY(360deg);
}
#tweet-list .tweet:nth-child(4n+1) .image{
    -webkit-transform-origin: 50% 0%;
    -moz-transform-origin: 50% 0%;
    -ms-transform-origin: 50% 0%;
    -o-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
}
#tweet-list .tweet.show:nth-child(4n+1) .image{
    -webkit-transform: rotateX(360deg);
    -moz-transform: rotateX(360deg);
    -ms-transform: rotateX(360deg);
    -o-transform: rotateX(360deg);
    transform: rotateX(360deg);
}
#tweet-list .tweet:nth-child(4n+2) .image{
    -webkit-transform-origin: 50% 0%;
    -moz-transform-origin: 50% 0%;
    -ms-transform-origin: 50% 0%;
    -o-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
}
#tweet-list .tweet.show:nth-child(4n+2) .image{
    -webkit-transform: rotateX(360deg);
    -moz-transform: rotateX(360deg);
    -ms-transform: rotateX(360deg);
    -o-transform: rotateX(360deg);
    transform: rotateX(360deg);
}
#tweet-list .tweet:nth-child(4n+3) .image{
    -webkit-perspective: 1000;
    -moz-perspective: 1000;
    -o-perspective: 1000;
    perspective: 1000;
}
#tweet-list .tweet.show:nth-child(4n+3) .image{
    -webkit-transform: rotateX(360deg);
    -moz-transform: rotateX(360deg);
    -o-transform: rotateX(360deg);
    transform: rotateX(360deg);
}
#tweet-list .tweet .thumb{
    -webkit-perspective: 1000;
    -moz-perspective: 1000;
    -o-perspective: 1000;
    perspective: 1000;
    -webkit-transition: all 1s ease-in-out;
    -moz-transition: all 1s ease-in-out;
    -o-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;
}
#tweet-list .tweet.imageFlip .thumb{
    -webkit-transform: rotateY(180deg);
    -moz-transform: rotateY(180deg);
    -o-transform: rotateY(180deg);
    transform: rotateY(180deg);
    -webkit-transition: all 0.1s ease-in-out;
    -moz-transition: all 0.1s ease-in-out;
    -o-transition: all 0.1s ease-in-out;
    transition: all 0.1s ease-in-out;
}
 
#tweet-list .tweet .thumb img {
    width: 100%; 
    height: 100%; 
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    max-width: none;
    left: 50%;
}
.content-inner .header h4 img {
    height: 41px;
    width: 41px;
    border-radius: 50%;
    float: left;
    margin: 0px 10px 0px 0px;
}
.content-inner .header h4 a {
    font: 600 16px/23px 'open sans';
    display: block;
    text-overflow: ellipsis;
    overflow: hidden;
    text-decoration: none;
    white-space: nowrap;
}
.content-inner .header h4 span {
    font: 400 11px/20px 'open sans';
    margin-right: 36px;
    display: block;
    margin-top: 0;
}
.content-inner .header .postNetwork {
    position: inherit;
    padding: 0px;
    margin-top: 5px;
    margin-left: 20px;
    float: right;
}
.content-inner .header .postNetwork i{ font-size: 25px;  line-height: 18px!important;}

