    -moz-transform-origin: none;
    -ms-transform-origin: none;
    -o-transform-origin: none;
    transform-origin: none;
	}
#tweet-list .tweet[data-tweet="video"] .tweet-post-img{
	visibility:hidden;
}
#tweet-list .tweet[data-tweet="video"] .tweet-post-img.tweet-post-video{
	-webkit-perspective: 1000;
    -moz-perspective: 1000;
    -o-perspective: 1000;
    perspective: 1000;
	visibility:hidden;
	-webkit-transition: all 1s ease-in-out;
    -moz-transition: all 1s ease-in-out;
    -o-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;
	opacity:0 !Important;
	position:absolute;
	left:-150px;
	top:0;
	transform:scale(0.5, 0.5) rotateY(360deg);
	-webkit-transform:scale(0.5, 0.5) rotateY(360deg);
	-moz-transform:scale(0.5, 0.5) rotateY(360deg);
	-o-transform:scale(0.5, 0.5) rotateY(360deg);
	-ms-transform:scale(0.5, 0.5) rotateY(360deg);    
}
#tweet-list .tweet.show[data-tweet="video"] .tweet-post-img.tweet-post-video{
	visibility:visible; opacity:1 !Important;
	left:0;
	top:0;
	transform:scale(1, 1) rotateY(0deg);
	-webkit-transform:scale(1, 1) rotateY(0deg);
	-moz-transform:scale(1, 1) rotateY(0deg);
	-o-transform:scale(1, 1) rotateY(0deg);
	-ms-transform:scale(1, 1) rotateY(0deg);
}
#tweet-list .tweet.show .selected-item .sepratedot{
font-size: 4px;
margin-right: 4px!important;
}

#tweet-list .tweet.show .selected-item .content-inner .header h4 span{
margin-right:0px;
}
.tweet.show .tweet-post-img.tweet-post-video{
    background:#000 !important;
    min-width:350px;
    /* width:auto !important; */
    height:auto !important;    
    max-height:450px !important;
    max-width:350px !important; 
    box-shadow: black 0px 0px 30px;
    -webkit-box-shadow: black 0px 0px 30px;
    -moz-box-shadow: black 0px 0px 30px;
	border: 5px solid #fff; 	
}
.tweet.show .tweet-post-img.tweet-post-video video, .tweet.show .tweet-post-img.tweet-post-video iframe{ 
    display:block;      
    border:0px; 
    font-size:0px;
    min-width:340px;
	min-height:340px;
    width:340px !important;
	max-height:100% !important;
    /* max-width:340px !important; */
	height:auto !important;		    
}