.popupModalBg {
    background-color: #000;
    height: 0%;
    width: 0%;
    position: fixed;
    top: 0;
    z-index: 1002;
}
.taggModal .clear {
    height: 1px;
}
.taggModalShow.fade {
    opacity: 0;
    -webkit-transition: none;
    -moz-transition: none;
    -o-transition: none;
    transition: none;
}
.taggModalShow.fade .taggModal {
    background: #fff;
    position: fixed;
    z-index: 10050;
    -moz-box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
    -webkit-box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
    width: 60vw;
    top: 50%;
    margin-top: -100%;
    left: 20vw;
    right: 20vw;
    transition: all ease 0.3s !important;
    -moz-transition: all ease 0.3s !important;
    -webkit-transition: all ease 0.3s !important;
    -o-transition: all ease 0.3s !important;
    -moz-transform: scale(1, 1);
    -ms-transform: scale(1, 1);
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
}
.popupModalBg,
.taggModalShow.fade .taggModal {
    opacity: 0;
    -moz-transition-property: -moz-transform, -webkit-transform, -moz-transform, -ms-transform, opacity;
    -o-transition-property: -o-transform, -webkit-transform, -moz-transform, -ms-transform, opacity;
    -webkit-transition-property: -webkit-transform, -webkit-transform, -moz-transform, -ms-transform, opacity;
    transition-property: transform, -webkit-transform, -moz-transform, -ms-transform, opacity;
    -moz-transition-duration: 0.2s;
    -o-transition-duration: 0.2s;
    -webkit-transition-duration: 0.2s;
    transition-duration: 0.2s;
    -moz-transition-timing-function: ease-in;
    -o-transition-timing-function: ease-in;
    -webkit-transition-timing-function: ease-in;
    transition-timing-function: ease-in;
}
.taggModalShow.fade.in {
    opacity: 1;
}
.taggModalShow .popupModalBg {
