/* Full Screen Announcement */

.taggModalShow.fade.anouncementModal .taggModal {
    width: 100vw!important;
    height: 100vh;
}

.anouncementModal .taggContentHolder {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.taggContentHolder {
    position: absolute;
    height: 100%;
    width: 44%;
    right: 0;
    top: 0;
    padding: 1.5vw;
}

.taggImageHolder {
    background: center center no-repeat #000;
    background-size: contain;
    position: absolute;
    width: 56.25%;
    top: 0;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
}
.taggModalShow.disModalPopup.modal-text-image .setFont {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 130%;
}

.taggModalShow.disModalPopup .setFont {
    font-size: 25px;
    line-height: 125%;
}
.taggModalShow .setFont {
    word-break: break-word;
    color: #323232;
}

.anounceTxtOnly .setFont.only-text {
    font-weight: 400;
}

.taggImageHolder img {
    max-width: 100%;
    height: auto;
    width: auto;
    display: block;
}
.taggModalShow.modal-only-image.fade .taggModal .taggImageHolder, .taggModalShow.modal-only-video.fade .taggModal .taggImageHolder {
    width: 100%;
    position: static;
    max-height: 60vh;
}
.taggModalShow.modal-only-image.fade.anouncementModal .taggModal .taggImageHolder {
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    height: 100%;
    max-height: 100%;
}

.taggModalShow.modal-only-image.anouncementModal .taggImageHolder img {
    visibility: hidden;
}
.taggModalShow.modal-only-image .taggImageHolder img {
    visibility: visible;
    max-height: inherit;
    max-width: 100%;
    width: auto;
    margin: 0 auto;
}

.modal-only-text .taggContentHolder {
    width: 100%;
}

.anouncementModal .setFont.only-text {
    width: 90%;
    text-align: center;
    margin: 0 auto;
}

.instaMainImg img {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    height: 100%;
    margin: 0 auto;
    max-height: 100%;
}
