.rhap_container.tb_post_modal_audio {
    width: 100%;
    padding: 10px 0px;
    background-color: transparent;
    box-shadow: none;
}

.tb_post_modal_audio_thumb_blur {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    filter: blur(14px);
    background-color: #000;
    position: relative;
}

.tb_post_modal_audio_thumb {
    z-index: 1;
    width: 70%;
    height: 70%;
    position: absolute;
    left: 0;
    right: 0;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    margin: 0 auto;
    bottom: 20%;
}

.tb_post_modal_audio_wrapper {
    position: absolute;
    z-index: 1;
    bottom: 5%;
    left: 0;
    width: 100%;
    right: 0;
    max-width: 80%;
    margin: 0 auto;
}

.tb_post_modal_audio_thumb_blur:after {
    content: '';
    position: absolute;
    background-color: rgba(0,0,0,.4);
    z-index: 1;
    width: 100%;
    height: 100%;
    filter: blur(14px);
    transform: scale(1.5);
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

